import React from 'react'
import PropTypes from 'prop-types'

function Spacer (props) {
	let style = {
	  width: props.width,
	  height: props.height,
	  marginLeft: parseInt(props.width) < 0 ? props.width : undefined,
	  marginTop: parseInt(props.height) < 0 ? props.height : undefined
	}
	return (
	  <div className="spacer" style={style}></div>
	)
}

Spacer.propTypes = {
	width: PropTypes.oneOfType([
		PropTypes.number,
		PropTypes.string
	]),
	height: PropTypes.oneOfType([
	  PropTypes.number,
	  PropTypes.string
	])
}

export default Spacer