import React from 'react'
import PropTypes from 'prop-types'

import Minuta from '../Minuta.js'
import { postos, grausSigilo, prioridades, usuarioAtual } from '../lib/globalData.js'
import htmlTel from '../htmltel.js'
import './Painel-Minuta.css'

function obterNomePosto (sigla) {
	const posto = postos.find(posto => sigla === (posto.sigla || posto.id))
	if (!posto) 
		return ''
  const nomePosto = posto.nome || posto.text
  const componentesNomePosto = nomePosto.split(' ')
  // Uma vez que temos o nome do posto, vamos colocar 
  // o nome da cidade em negrito
  if (componentesNomePosto.length === 1) {
  	// No caso em que o posto é identificado por uma sigla 
    // (p.ex. DELBRASONU), todo o nome aparece em negrito
  	return '<b>' + nomePosto + '</b>'
  }	
  componentesNomePosto.splice(1, 0, '<b>')
  return componentesNomePosto.join(' ') + '</b>'
}

function montaListaNomesPostos (arraySiglas, primeiroElemento) {
	if (!arraySiglas || !arraySiglas.length)
		return ''
  let nomesPostos = arraySiglas.map(sigla => obterNomePosto(sigla))
  // COMO FICAMOS COM AS RETRANSMISSÕES PARA GRUPOS??
  let htmlListaPostos = nomesPostos.join(',<br />')
  if (primeiroElemento)	{
  	htmlListaPostos = primeiroElemento + '<br />' + htmlListaPostos
  }
  return htmlListaPostos
}

function montarCabecalho (tipo, origemTel, destinatarioDet) {
	switch (tipo.charAt(0).toLowerCase()) {
		case 't': 
		  return (
		  	<>
		  	  <h2 dangerouslySetInnerHTML= {{ __html: obterNomePosto(origemTel) }}></h2>
		  	  <h3>Minuta de telegrama</h3>
		  	</>  
	  	)
	  case 'd':
  	  return (
  	  	<> 
  	  	  <h2>Da SERE para <span dangerouslySetInnerHTML= {{ __html: obterNomePosto(destinatarioDet) }}></span></h2>
  	  	  <h3>Minuta de despacho telegráfico</h3>
  	  	</>
	    )
	  case 'c':
	    return (
	      <>
		      <h2>Secretaria de Estado das Relações Exteriores</h2>
		      <h3>Minuta de circular telegráfica</h3>
	      </>
	    )
	  default:   
	    return <h2>Minuta de expediente telegráfico</h2>	
	}
}

function encontrarLabelCampo (idProcurado, arrayValores) {
  let campoObj = arrayValores.find(({ id }) => id === idProcurado)
  if (campoObj)
  	return campoObj.text
  return ''
}  


function PainelMinuta (props) {
	if (!props.minuta) {
		return (
      <>
      <br />
      <h3>{ props.placeholder || 'Selecione uma minuta na lista à esquerda' }</h3>
      </>
		)
  }
  const origemTel = usuarioAtual.posto
	const {
		tipo,
		destinatarioDet,
		distribuicao,
		rtm,
		destinatariosCit,
		indexadores,
		prioridade,
		carater,
		referencias,
		indice,
		proposito,		
		redator,		
		resumo,
		texto
	} = (new Minuta(props.minuta))  
  let textoComProposito
  if (!htmlTel.validar(texto)) {
  	textoComProposito = 'Corpo de texto inválido.'
  }
  else {
    textoComProposito = htmlTel.inserirProposito(texto, proposito) + '<p class="redator">' + redator + '</p>'
  }  

  return (
    <div style={ props.style }>
    <div className="cabecalho">
			<div className="posto-e-tipo">
			  { montarCabecalho(tipo, origemTel, destinatarioDet) }
			</div>	 
			<div className="campos-expediente">				 
				<div className="flex-container">
					<fieldset className="c1-1"><legend>Distribuição</legend>{ distribuicao.join(' / ') }</fieldset>
				</div>
				<div className="flex-container">
					<fieldset className="c2-3"><legend>Indexadores</legend>{ indexadores.join('-') }</fieldset>						
					<fieldset className="c1-3"><legend>Prioridade</legend>{ encontrarLabelCampo(prioridade, prioridades) }</fieldset>
				</div>
				<div className="flex-container">
					<fieldset className="c1-3"><legend>Caráter</legend>{ encontrarLabelCampo(carater, grausSigilo) }</fieldset>						
					<fieldset className="c1-3"><legend>Prazo de sigilo</legend></fieldset>						
					<fieldset className="c1-3"><legend>Base legal</legend></fieldset>
				</div>
				<div className="flex-container">
					<fieldset className="c1-1"><legend>Referências</legend>{ referencias.join(', ').toUpperCase() }</fieldset>
			  </div>				 
				<div className="flex-container">
					<fieldset className="c1-1"><legend>Índice</legend>{ indice }</fieldset>
				</div>				 				 
  		</div>				 

		</div>
			 
		<table>
		  <tbody>
		  <tr><td>
		    <div className="corpo-texto">
					<p className="rtm" dangerouslySetInnerHTML= {{ __html: montaListaNomesPostos(rtm, 'Retransmissão automática para') }}>
					</p>
					<p className="destinatarios" dangerouslySetInnerHTML= {{ __html: montaListaNomesPostos(destinatariosCit, 'Destinatários da circular telegráfica:') }}>
					</p>
					<p className="resumo">
						{ resumo }
					</p>	
					<div dangerouslySetInnerHTML={{ __html: textoComProposito }}></div>
	      </div>	
  		</td></tr>
  		</tbody>
		  <tfoot>
		  <tr><td className="content-space-below">&nbsp;</td></tr>
		  </tfoot>
		</table>

	  <div className="tci">
		  <h3>TCI</h3>
		  <br />
			<div className="flex-container" style={{ fontSize: 11 }}>
				<fieldset className="c1-4"><legend>Grau de sigilo</legend>Ultrassecreto</fieldset>						
				<fieldset className="c1-4"><legend>Prazo</legend>25 anos</fieldset>						
				<fieldset className="c1-2"><legend>Fundamento legal</legend>Lei 12.527/11 Art. 23 - II</fieldset>
			</div>  	
	  	<div className="flex-container">
	  		<fieldset className="c1-1"><legend>Razões para classificação</legend>O expediente relata gestão junto à Chancelaria e ao Ministério do Esporte de Neutrália em favor da candidatura do Sr. José da Silva para a Direção Geral da FIFA</fieldset>
	  	</div>	
	  </div>
    </div>
  )
}

PainelMinuta.propTypes = {
  minuta: PropTypes.object,  
  placeholder: PropTypes.string,
  style: PropTypes.object
}

export default PainelMinuta