import React from 'react'
import { components } from 'react-select'
import Select from 'react-select/async'

const LIGHT_TEXT_COLOR = '#182026' 
const DARK_TEXT_COLOR = 'white'
const DARK_BACKGROUND_COLOR = 'rgba(16, 22, 26, 0.3)'
const DARK_MENU_BACKGROUND_COLOR = 'rgb(48, 64, 77)'
const DEFAULT_FONT_SIZE = '14px'  //'13.3333px'

const customStyles = {
  container: (provided, state) => {
    return {
      ...provided,
    }
  },

  control: (provided, state) => {

    let boxShadow = '0 0 0 0 rgb(19 124 189 / 0%), 0 0 0 0 rgb(19 124 189 / 0%), inset 0 0 0 1px rgb(16 22 26 / 15%), inset 0 1px 1px rgb(16 22 26 / 20%)'
    if (state.isFocused) {
      boxShadow = '0 0 1px 1px rgb(19, 124, 189), 0 0 1px 3px rgba(19, 124, 189, 40%)'
    }
    return {
      ...provided,
      minHeight: '1px',
      cursor: 'pointer',
      fontSize: DEFAULT_FONT_SIZE,
      color: LIGHT_TEXT_COLOR,      
      borderRadius: 3,
      border: 'none',      
      outline: 'none',
      boxShadow,
    }
  },

  input: (provided, state) => {
    return {
      ...provided,
      color: 'white'
    }
  },

  menu: (provided, state) => {
    return {
      ...provided,
      marginTop: -1,
      marginLeft: -2,
      paddingLeft: 4,
      paddingRight: 0,
      borderRadius: 2,
      width: 'calc(100% + 4px)',
      cursor: 'pointer',
    }
  },

  menuList: (provided, state) => {
    return {
      ...provided,
      textAlign: 'left',
      cursor: 'pointer',
      paddingRight: 4,
    }
  },

  option: (provided, state) => {
    let colors = {
      backgroundColor: 'transparent',
      color: LIGHT_TEXT_COLOR,
    }
    if (state.isFocused) {
      colors = {
        backgroundColor: '#137cbd',
        color: 'white'        
      }
    }
    return {
      ...provided,
      cursor: 'pointer',
      borderRadius: 2,
      fontSize: DEFAULT_FONT_SIZE,
      padding: 6,
      paddingLeft: 8,
      ...colors  
    }  
  },

  indicatorSeparator: (provided, state) => {
    return {
      ...provided,
      backgroundColor: 'transparent'
    }  
  },

  dropdownIndicator: (provided, state) => {
    return {
      ...provided,
      padding: '0px 4px',
    }
  },

  singleValue: (provided, state) => {
    return {
      ...provided,
      color: LIGHT_TEXT_COLOR,
      marginLeft: '-0.2px',
    }  
  }
}

function removerAcentos (str = '') {
  return str.toString().normalize("NFD").replace(/[\u0300-\u036f]/g, "")
}

const defaultValueRenderer = (data) => {
  return data.text
}

function CustomSingleValue (valueRenderer=defaultValueRenderer) {
  return ({ children, ...props }) => {
    return (
      <components.SingleValue {...props}>{ valueRenderer(props.data) }</components.SingleValue>
    )
  }  
}

const defaultItemRenderer = ({ text, label, id }) => {
  return (
    <div style={{ display: 'flex' }}>
      <div style={{ flex: 1 }}>{ text }</div>
      <div style={{ opacity: 0.9, fontSize: '0.9em', paddingLeft: 4 }}>{ label }</div>
    </div>
  ) 
}

function CustomOption (itemRenderer=defaultItemRenderer) {
  return (props) => (
    <components.Option {...props}>
      { itemRenderer(props.data) }
    </components.Option>  
  )  
}

function strIncludes (str='', substr) {
  if (!substr) 
    return false
  return removerAcentos(str).toLowerCase().includes(substr)
}

function defaultFilterItems (items=[]) {
  return (inputValue) => new Promise(resolve => {
    if (!inputValue.trim() || !items || !items.length) {
      resolve([])
      return
    }
    inputValue = removerAcentos(inputValue.trim()).toLowerCase()
    const filteredUsers = items
      .filter(item => strIncludes(item.text, inputValue) || 
                      strIncludes(item.label, inputValue) || 
                      strIncludes(item.value, inputValue))
      .sort((a, b) => a.text.localeCompare(b.text, 'pt-br', { sensitivity: 'base' }))
    resolve(filteredUsers)    
  })
}  

function handleChange (e) {
  console.log(e)
  debugger
}

function noOptionsMessage (typeToSearchMessage='Type to search...', noOptionsMessage='No options') {
  return ({ inputValue }) => inputValue ? noOptionsMessage : typeToSearchMessage
}

function AsyncSelect (props) {
  return (
    <Select 
      {...props}
      width={ props.fill ? '100%' : props.width }
      loadingMessage={ () => props.loadingMessage || 'Loading...' }
      noOptionsMessage={ noOptionsMessage(props.typeToSearchMessage, props.noOptionsMessage) }
      isClearable={false}
      isSearchable={true}
      styles={props.customStyles ? {...customStyles, ...props.customStyles} : customStyles}  
      components={{ 
        Option: CustomOption(props.itemRenderer), 
        SingleValue: CustomSingleValue(props.valueRenderer)
      }} 
      menuShouldBlockScroll={false}
      captureMenuScroll={true}
      menuPortalTarget={document.getElementById('root')}
      menuPlacement={props.menuPlacement || 'auto'}
      maxMenuHeight={props.maxMenuHeight || 314}
      loadOptions={props.searchItems || props.filterItems || defaultFilterItems(props.items)}
      onChange={props.onItemSelect || props.onChange}
    />
  )
}

export default AsyncSelect
