const minutaCarregada = {
  tipo: 'det',
  destinatarioDet: 'USABREM',
  rtm: ['CANBREM'],
  carater: 'r',
  distribuicao: ['DINFOR', 'DEUA', 'DAM II'],
  indexadores: ['AINF', 'PEXT', 'BRAS'],
  referencias: ['TEL 1987/2020'],        
  indice: 'Tecnologia da Informações. Comunicações. Reflexões sobre o SATCOM',
  redator: 'FACC',
  autoridade: 'Julio Bitelli',
	texto: `<p>Hoje, com efeito, a tendência de alguns Chefes de Missão é, na medida em que existem a seu dispor meios rápidos e confiáveis de comunicações, a de transmitir ao órgão central a maior quantidade possível de informações, quando mais não seja para demonstrar a eficiência de seu trabalho. Nesse afã de informar, entretanto, frequentemente lhes falta o tempo para tarefas mais relevantes, quais sejam, a avaliação da informação, sua inserção no quadro da realidade do país em que servem, a análise de suas possíveis ou prováveis repercussões sobre a política externa e interna desse país sobre seu relacionamento com o Brasil: em suma, o processamento primário dos dados coletados. Como resultado, o que é enviado à Secretaria de Estado são, muitas vezes, "pílulas" de informações descosidas que transferem, para o destinatário, a tarefa de montar o quadro geral da situação, tarefa essa qie seria de muito mais fácil realização, em uma primeira instância, pelo próprio agente diplomático <em>in loco</em>.</p>
<p>O uso cada vez mais frequente da via telegráfica tem contribuído para acelerar essa tendência, uma vez que se considera, em muitos casos, que o mais importante é a rapidez com que a Missão infoma sobre um acontecimento ou transmite uma informação. Na verdade, entretanto, a informação diplomática tem sua utilidade avalidada em função de três aspectos principais: a qualidade de sua fonte, a fidedignidade da própria informação e a sua análise no contexto global da realidade em que se insere. A rapidez com que ela é recebida na Secretaria de Estado assume, assim, na maioria das vezes, importância menor. Isso não quer dizer, evidentemente, que o diplomata não deva preocupar-se em informar seu governo, com a possível brevidade, da ocorrênci, no país em que serve, de fatos de maior relevância. Significa, na verdade, que sua função básica, enquanto observador e informante, não é a de fazer concorrência à imprensa e sim a de fornecer, ao órgão central, dados que transcendam à mera descrição dos acontecimentos.</p>
<p>Em consequência, seria muito mais útil e proveitoso, para a correta formulação e execução da política externa, se o posto, ao invés de enviar informações tópicas, as reunisse e as transmitisse de forma selecionada e condensada e acompanhadas de umam reflexão analítica sobre suas reais implicações. Uma vez recebidas na Secretaria de Estado, tais informações, utilizando-se agora o termo em sua correta concepção, seriam novamente analisadas, dessa vez em conjunto com informações originárias de terceiros postos e de outras fontes.</p>
<p>Aliás, a bem da verdade, já foram feitas algumas tentativas no sentido de que as missões passassem a informar dessa maneira. Quando da mudança da Secretaria de Estado para Brasília, por exemplo, o grupo de trabalho encarregado da organização da transferência previu que, durante um certo tempo, as atividades de comunicações telegráficas e postais perderiam muito de sua agilidade, em decorrência de problemas ligados a pessoal e também à instalação, nos novos locais, das facilidades mínimas necessárias à boa execução de seus serviços. Em vista disso, e com o objetivo de evitar que tais inconvenientes redundassem em maiores prejuízos para as atividades básicas do Ministério, foram transmitidas instruções aos postos para que substituíssem, na medida do possível, o envio de informações soltas por relatórios abrangentes e aprofundados sobre os diversos aspectos dos países em que se localizavam. Tais instruções salientavam o fato de que, durante esse período de transição, as comunicações telegráficas deveriam ser reduzidas ao mínimo necessário.</p>
<p>Essa diretriz, embora motivada estritamente por razões operacionais, apresentou notáveis resultados do ponto de vista substantivo. Com efeito, impedidos de fazerem telegramas tópicos, os diplomatas lotados no exterior puderam dedicar-se à elaboração de informações globais e de grande substância e que, ainda hoje, decorridos quase 15 anos, em muitos casos conservam sua importância e seu interesse.</p>
<p>Infelizmente, essa atitude não durou muito, e tão logo os postos foram sentindo que os trabalhos na Secretaria de Estado estavam voltando à normalidade, retomaram eles, paulatinamente, seus procedimentos anteriores em matéria de transmissões de informações tópicas por via telegráfica.</p>
<p>(...) </p>
<p>Tudo isso é resultado, pelo menos em parte, de certa deformação profissional que leva alguns Chefes de Missão a considerarem o grau de importância de seu posto diretamente proporcional ao número de telegramas por ele expedido. Dentro dessa ótica, quaisquer iniciativas que visem, direta ou indiretamente, a redução desse número terão poucas chances de serem bem aceitas.</p>
<p>TAVARES, Alfredo Carlos de Oliveira. <em>O subsistema de comunicações telegráficas do Itamaraty.</em> Tese apresentada no IX CAE. Brasília, agosto de 1984, pp. 123-127.</p>
<p></p>
<p></p>
<p></p>
<p></p>
<p></p>
<p></p>`
}

