const versoes = [
  '2020-12-30 10:45',
  '2020-12-29 20:30',
  '2020-12-29 15:30',
  '2020-12-25 10:00'
].sort().map((data, index) => {
  return {
    title: `Versão ${index+1}`,
    data: data.split(' ')[0],
  }
}).reverse()


function mockSave (func) {
  return new Promise(function (resolve, reject) {
    let data = func()
    setTimeout(() => {
      if (data) {
        resolve(data)
      }
      else {
        reject(new Error('Ocorreu um erro'))
      }
    }, 1500)
  })
}

function carregarMinuta () {
  return new Promise(function (resolve, reject) {
    setTimeout(() => {
      resolve(minutaCarregada)
    }, 2000)
  })
}

function salvarMinuta (minuta) {
  return new Promise(function (resolve, reject) {
    console.log('Autosaving...')
    console.log(minuta)
    resolve(minuta)
  })
}


function carregarDespachos () {
  return new Promise(function (resolve, reject) {
    setTimeout(() => {
      resolve(despachos)
    }, 1000)
  })
}

function adicionarDespacho (novoDespacho) {
  return new Promise(function (resolve, reject) {
    setTimeout(() => {
      despachos.push(novoDespacho)  
      resolve(despachos)
    }, 1000)
  })
}

function carregarModelos () {
  const promiseFunction = function (resolve, reject) {
    setTimeout(() => {
      const grupos = [{
        nome: 'Meus modelos',
        minutas: modelos.privados
      }, {
        nome: 'Modelos da unidade',
        minutas: modelos.publicosUnidade,
      }, {
        nome: 'Modelos públicos',
        minutas: modelos.publicos
      }] 
      resolve(grupos)
    }, 2000)
  } 
  return new Promise(promiseFunction) 
}

function salvarModelo (titulo, tipo) {
  return mockSave(() => {
    const tipos = {
      'privado': 'privados',
      'unidade': 'publicosUnidade',
      'publico': 'publicos'
    }   
    let arrTipo = modelos[tipos[tipo]]
    if (!arrTipo) 
      return false
    arrTipo.push(titulo)
    return titulo
  })
}

function carregarRubricas () {
  return new Promise(function (resolve, reject) {
    setTimeout(() => resolve(rubricas), 5000)
  })
}

function carregarVersoes () {
  return new Promise(function (resolve, reject) {
    setTimeout(() => resolve(versoes.map(versao => {
      return {
        ...versao,
        label: versao.data
      }
    })), 1500)
  })  
}

function salvarVersao (titulo) {
  return mockSave(() => {
    versoes.unshift({
      title: titulo,
      data: '2021-01-02'
    })
    return true
  })
}

function salvarCopia (titulo) {
  return mockSave(() => true)
}

export {
  adicionarDespacho,
  carregarMinuta,
  carregarDespachos,
  carregarModelos,
  carregarRubricas,
  carregarVersoes,
  salvarCopia,
  salvarModelo,
  salvarMinuta,
  salvarVersao
}