import React from 'react'
import PropTypes from 'prop-types'

import { Alignment, Button, Classes, EditableText, Navbar, NavbarGroup, NavbarHeading } from '@blueprintjs/core'
import Spacer from './ui/Spacer.js'
import './Topbar.css'

function Topbar (props) {
	const sidebarIcon = props.sidebarIsOpen ? 'menu-closed' : 'menu'
	const rightbarIcon = props.rightbarIsOpen ? 'menu-open' : 'menu'
	return (
	  <Navbar className="bp3-dark">
	      <NavbarGroup>
	          <Spacer width="-12px" />
	          <Button className={Classes.MINIMAL} icon={sidebarIcon} onClick={props.toggleSidebar} />
	          <Spacer width="16px" />
	          <NavbarHeading>
	            <div style={{ maxWidth: 400 }}>
		            <EditableText
		              maxLength={120}
		              placeholder="Clique para dar um nome a esta minuta..."
		              onChange={ (e) => props.onChangeName && props.onChangeName(e) }
		            />
	            </div>
            </NavbarHeading>
	      </NavbarGroup>
	      <NavbarGroup align={Alignment.RIGHT} style={{ marginRight: -8 }}>
	        <Button className={Classes.MINIMAL} icon={rightbarIcon} onClick={props.toggleRightbar} />
	      </NavbarGroup>
	  </Navbar>
	)
}

Topbar.propTypes = {
	rightbarIsOpen: PropTypes.bool,
	sidebarIsOpen: PropTypes.bool,
	onChangeName: PropTypes.func,
	toggleRightbar: PropTypes.func,
	toggleSidebar: PropTypes.func
}

export default Topbar

    