import React from 'react'
import PropTypes from 'prop-types'

import { Menu, MenuDivider, MenuItem } from '@blueprintjs/core'
import './ListaMinutas.css'

class ListaMinutas extends React.Component {

  constructor (props) {
  	super()
  	this.state = {
  		selected: null
  	}
  	this.handleMenuItemClick = this.handleMenuItemClick.bind(this)
  }

  handleMenuItemClick (item) {
  	return (e) => {
  		this.setState({
  			selected: item
  		})
  		this.props.onClick(item)
  	}
  }

  render () {
	  const listaHtml = this.props.grupos.map(grupo => {
		  const modelosHtml = grupo.minutas.map(minuta => {
		  	const label = <small>{ minuta.label }</small>
		    return (
	 				<MenuItem 
	 				  text={ minuta.title || minuta.texto || minuta } 
	 				  labelElement={ label }
	 				  onClick={ this.handleMenuItemClick(minuta) }
	 				  multiline={ true }
	 				  active={ this.state.selected === minuta }
	 				/>
		    )
		  })
		  if (modelosHtml.length) {
		  	if (grupo.nome) {
		  	  modelosHtml.unshift(<MenuDivider title={ grupo.nome }/>)
		  	}
		  	return modelosHtml
		  }
			else {
			  return ''
			}
		})
		return (
	    <Menu> 
	      { listaHtml }
	    </Menu>
	  )
  }
}  

ListaMinutas.propTypes = {
	grupos: PropTypes.array,
	onClick: PropTypes.func
}

export default ListaMinutas

           