import React from 'react'
import PropTypes from 'prop-types'
import './CollapsibleBar.css'

function CollapsibleBar (props) {
	let style = {
		width: props.width || props.defaultWidth 
	}
	if (!props.open) {
		style.width = 0
	}
	
	return (
		<div className="collapsible-bar" style={style}>
		  { props.children }
		</div>	
	)
}

CollapsibleBar.propTypes = {
	defaultWidth: PropTypes.oneOfType([
	  PropTypes.number,
	  PropTypes.string
	]),
	open: PropTypes.bool.isRequired,
	width: PropTypes.oneOfType([
	  PropTypes.number,
	  PropTypes.string
	]),	
}

export default CollapsibleBar