import React from 'react'
import PropTypes from 'prop-types'

import './FloatingField.css'

function FloatingField (props) {
	let button
	if (props.buttonIcon && props.buttonClick) {
    button = <button onClick={ props.buttonClick }>{ props.buttonIcon }</button>
	}	
	let classList = ['formfield', props.className]
	if (props.disabled) {
		classList.push('disabled')
	}
	if (props.error) {
		classList.push('has-error')
	}
	if (props.placeholder) {
		classList.push('not-empty')
	}
	const style = {
		flex: 1,
	  width: props.width,
	  ...props.style
	}
	if (props.visible === false) 
		style.display = 'none'
	return (
	  <div className="formfield-container" style={ style }>
			<div 
			  className={ classList.join(' ') }
			  onClick={ props.disabled ? () => {} : props.onClick } 
			  onBlur={ props.onBlur }
			>
	      { props.children }
				<label >{ props.label }</label>
				{ button }
			</div>
			<div className={ props.error ? 'error message' : 'message' }>{ props.error || props.message }</div>
		</div>
  )
}

FloatingField.propTypes = {
	buttonIcon: PropTypes.element,
	className: PropTypes.string,
	error: PropTypes.string,
	label: PropTypes.string,
	message: PropTypes.string,
	placeholder: PropTypes.string,
	width: PropTypes.oneOfType([
	  PropTypes.number,
	  PropTypes.string
	]),
	visible: PropTypes.bool,
	buttonClick: PropTypes.func,
	onBlur: PropTypes.func,
	onClick: PropTypes.func,
}

export default FloatingField