import React from 'react'
import PropTypes from 'prop-types'
import { Button, Dialog, Classes, Intent, InputGroup, Label, Radio, RadioGroup } from '@blueprintjs/core';
import Spacer from '../ui/Spacer.js'

class DialogSalvarModelo extends React.Component {
	constructor (props) {
	  super()
	  this.state = {
	  	value: '',
	  	tipoModelo: null,
	  }
	  this.handleInputChange = this.handleInputChange.bind(this)
		this.handleTipoModeloChange = this.handleTipoModeloChange.bind(this)	  
	  this.handleClickOk = this.handleClickOk.bind(this)
	  this.handleClose = this.handleClose.bind(this)	  
	}

  componentDidUpdate (prevProps) {
    if (this.props.show && !prevProps.show) {
    	this.setState({
    		value: ''
    	})
    }
  }	

	handleInputChange (e) {
		this.setState({
			value: e.target.value
		})
	}

	handleTipoModeloChange (e) {
		this.setState({
			tipoModelo: e.target.value
		})
	}

	handleClickOk (e) {
		this.props.onChange && this.props.onChange({
			titulo: this.state.value,
			tipo: this.state.tipoModelo
		})
		this.handleClose(e)
	}

	handleClose (e) {
		this.props.onClose && this.props.onClose() 
	}		

  render () {
		const ButtonStyle = { minWidth: 100 }
		return (
			<Dialog
			  style={{ width: 480 }}
	      onClose={this.handleClose}
	      title={this.props.title || 'Salvar esta minuta como modelo'}
	      isOpen={this.props.show}
				canOutsideClickClose={false}
	    >
	      <div className={Classes.DIALOG_BODY}>
	        <Label>
	          Indique um nome para o modelo:
	        </Label>  
		  		<InputGroup
			      asyncControl={true}
			      placeholder={this.props.placeholder}
			      onChange={this.handleInputChange}
			      value={this.state.value}
			    />	
			    <Spacer height={16} />
					<RadioGroup
					    label="Indique quem poderá usar este modelo:"
					    onChange={this.handleTipoModeloChange}
					    selectedValue={this.state.tipoModelo}
					>
					    <Radio label="Somente eu" value="privado" />
					    <Radio label="Minha unidade" value="unidade" />
					    <Radio label="Todos os usuários" value="publico" />
					</RadioGroup>
			  </div>
		    <div className={Classes.DIALOG_FOOTER}>
	        <div className={Classes.DIALOG_FOOTER_ACTIONS}>	        
	          <Button fill={false} style={ButtonStyle} onClick={ this.props.onClose }>Cancelar</Button>
	          <Button fill={false} 
	            style={ButtonStyle} 
	            type="submit" 
	            intent={Intent.PRIMARY} 
	            disabled={!this.state.value || !this.state.tipoModelo}
	            onClick={ this.handleClickOk }>Salvar como modelo</Button>
	        </div>  
	      </div> 
	    </Dialog>
    )   
	}
}

DialogSalvarModelo.propTypes = {
	label: PropTypes.string,
	placeholder: PropTypes.string,
	show: PropTypes.bool.isRequired,
	title: PropTypes.string,
	onChange: PropTypes.func.isRequired,
	onClose: PropTypes.func.isRequired,
}

export default DialogSalvarModelo