import { firstCharToLowerCase, mergeArrays } from './lib/utils.js'
import { prepararArquivoExpedicao, prepararObjetoExpedicao } from './lib/expedicaoMinuta.js'

// As constantes abaixo foram chutadas; tem que verificar 
// o tamanho máximo determinado pelo BD

const MAX_INDICE_LENGTH = 120
const MAX_RESUMO_LENGTH = 500
const MAX_RAZOES_LENGTH = 500   

const labelsCampos = {
  'destinatarioDet': 'Destinatário do desptel',
  'destinatariosCit': 'Destinatários da circtel',
  'rtm': 'Retransmissão',
  'distribuicao': 'Distribuição',
  'indexadores': 'Indexadores',
  'indice': 'Índice',
  'carater': 'Caráter',
  'prioridade': 'Prioridade',
  'referencias': 'Referências',
  'proposito': 'Propósito',
  'redator': 'Iniciais do redator',
  'resumo': 'Resumo',
  'prazoSigilo': 'Prazo de sigilo',
  'baseLegalSigilo': 'Base legal',
  'autoridade': 'Autoridade classificadora',
  'cargo': 'Cargo',
  'razoes': 'Razões para classificação',
  'texto': 'Corpo do texto'
}

class Minuta {
	constructor (props={}) {
    this.titulo = props.titulo || ''
  	this.tipo = props.tipo || ''
  	this.destinatarioDet = props.destinatarioDet
  	this.destinatariosCit = props.destinatariosCit || []
		this.rtm = props.rtm || []  		
    this.rtmClic = props.rtmClic || []
    this.distribuicao = props.distribuicao || []  
  	this.indexadores = props.indexadores || []
  	this.indice = props.indice || ''
  	this.carater = props.carater
  	this.prioridade = props.prioridade
  	this.referencias = props.referencias || []
  	this.proposito = props.proposito || ''
    this.redator = props.redator || ''
  	this.resumo = props.resumo || ''
		this.prazoSigilo = props.prazoSigilo || ''
		this.baseLegalSigilo = props.baseLegalSigilo || ''
		this.autoridade = props.autoridade || ''
		this.cargo = props.cargo || ''
    this.razoes = props.razoes || ''
    this.texto = props.texto || ''
    this._changed = false
    Object.seal(this)
	}

  setProp (key, value) {
    if (isArray(value)) {
      value = value.map(objToStr)
    }
    else if (isObject(value)) {
      value = objToStr(value)
    }
    if (value === null) {
      // O sistema não deveria usar setProp com argumento value nulo
      console.error('setProp recebeu argumento value null com key ' + key)
      // return this
    }
    this[key] = value
    this._changed = true
    return this
  }

  setProps (obj, value) {
    if (typeof(obj) === 'string' && value !== undefined)
      this.setProp(obj, value)
    else {
      for (let prop in obj) {
        this.setProp(prop, obj[prop])
      }
    }  
    return this
  }

  foiAlterada () {
    return this._changed
  }

  marcarComoNaoAlterada () {
    this._changed = false
  }

  validate (propName) {
    const maxDistribuicoesPorSigilo = {
      o: 7,  // ostensivo
      r: 5,  // reservado
      s: 3,  // secreto
      u: 3   // ultrassecreto
    }
    const maxRtmPorSigilo = {
      o: 5,  // ostensivo
      r: 3,  // reservado
      s: 0,  // secreto
      u: 0,  // ultrassecreto
    }
    const maxDestinatariosPorSigilo = {
      o: Infinity, // ostensivo
      r: Infinity, // reservado,
      s: Infinity, // secreto
      u: 5, // ultrassecreto
    }
    const MAX_INDEXADORES = 5,
          MAX_RTM = 3,
          MAX_REF = 3

    const validations = {
      distribuicao: (value) => {
        const maxDistribuicoes = maxDistribuicoesPorSigilo[this.carater || 'o']
        if (value.length > maxDistribuicoes)
          return `Indique no máximo ${ maxDistribuicoes } unidades`
        return ''
      },  
      indexadores: (value) => value.length > MAX_INDEXADORES ? `Indique até ${ MAX_INDEXADORES } indexadores` : '',
      destinatariosCit: (value) => {
        const maxDestinatarios = maxDestinatariosPorSigilo[this.carater || 'o']
        if (value.length > maxDestinatarios)
          return `Indique no máximo ${ maxDestinatarios } postos`
        return ''
      },  
      rtm: (value) => {
        const maxRtm = maxRtmPorSigilo[this.carater || 'o']
        if (!maxRtm && value.length)        
          return 'Não é possível retransmitir expedientes secretos ou ultrassecretos'
        if (this.tipo === 'det' && this.destinatarioDet && value.indexOf(this.destinatarioDet) >= 0)
          return 'O destinatário do desptel não pode ser incluído na retransmissão'
        if (value.length > MAX_RTM)
          return `Indique no máximo ${ MAX_RTM } postos` 
        return ''
      },  
      rtmClic: (value) => {
        if (!value || !value.length) return ''
        const numRtm = value.length
        if (numRtm && ['s', 'u'].includes(this.carater))
          return 'Não é possível retransmitir expedientes secretos ou ultrassecretos'
        if (numRtm > 1)
          return 'É permitida a retransmissão para apenas um grupo'
        return ''
      },
      referencias: (value) => value.length > MAX_REF ? `Indique no máximo ${ MAX_REF } referências` : '',
      indice: (value) => validaTamanhoCampoTexto(value, MAX_INDICE_LENGTH, 'índice'),
      resumo: (value) => validaTamanhoCampoTexto(value, MAX_RESUMO_LENGTH, 'resumo'),
      razoes: (value) => validaTamanhoCampoTexto(value, MAX_RAZOES_LENGTH, 'razoes')
    }
  
    const errors = {}
    if (propName) {
      const validationFunction = validations[propName]
      if (validationFunction)
        return validationFunction(this[propName])
      return ''
    }
    // Se o argumento propName não foi especificado, valida todos os campos da minuta
    for (let prop in validations) {
      const validationFunction = validations[prop]
      if (!validationFunction)
        break
      const error = validationFunction(this[prop])
      if (error)
        errors[prop] = error
    }
    return errors
  }

  validarParaExpedir () {
    const erros = []
    let camposObrigatorios = []
    switch (this.tipo.charAt(0).toLowerCase()) {
      case 'd':
        camposObrigatorios = ['destinatarioDet']
        break;
      case 'c':
        if (this.destinatariosCit.length < 2) {
          erros.push(`Destinatários da circtel: indique no mínimo dois destinatários`)
        }
        break
      default:  
    }
    camposObrigatorios = [
      ...camposObrigatorios,
      'distribuicao', 
      'indexadores', 
      'indice', 
      'carater', 
      'prioridade', 
      'redator',
      'texto'      
    ]
    if (this.carater && this.carater.charAt(0).toLowerCase() !== 'o') {
      camposObrigatorios = [...camposObrigatorios, 'prazoSigilo', 'baseLegalSigilo', 'razoes']
    }

    camposObrigatorios.forEach(campo => {
      let valor = this[campo]
      if (!valor || !valor.length) {
        erros.push(`${ labelsCampos[campo] }: campo obrigatório`)
      }
    })

    for (let campo in this) {
      const erro = this.validate(campo)
      if (erro) {
        erros.push(`${ labelsCampos[campo] }: ${ firstCharToLowerCase(erro) }`)
      }
    }

    return erros
  }

  mergeWith (outraMinuta) {
    this.tipo = this.tipo || outraMinuta.tipo
    this.destinatarioDet = this.destinatarioDet || outraMinuta.destinatarioDet
    this.destinatariosCit = mergeArrays(this.destinatariosCit, outraMinuta.destinatariosCit)
    this.rtm = mergeArrays(this.rtm, outraMinuta.rtm)
    this.rtmClic = this.rtmClic || outraMinuta.rtmClic
    this.distribuicao = mergeArrays(this.distribuicao, outraMinuta.distribuicao)
    this.indexadores = mergeArrays(this.indexadores, outraMinuta.indexadores)
    this.indice = mergeStrings(this.indice, outraMinuta.indice)
    this.carater = this.carater || outraMinuta.carater
    this.prioridade = this.prioridade || outraMinuta.prioridade
    this.referencias = mergeArrays(this.referencias, outraMinuta.referencias)
    this.proposito = this.proposito || outraMinuta.proposito
    this.resumo = mergeStrings(this.resumo, outraMinuta.resumo)
    this.redator = outraMinuta.redator || this.redator
    this.prazoSigilo = this.prazoSigilo || outraMinuta.prazoSigilo
    this.baseLegalSigilo = this.baseLegalSigilo || outraMinuta.baseLegalSigilo
    this.autoridade = this.autoridade || outraMinuta.autoridade
    this.cargo = this.cargo || outraMinuta.cargo
    this.razoes = mergeStrings(this.razoes, outraMinuta.razoes)
    this.texto = mergeStrings(this.texto, outraMinuta.texto)
    this._changed = true
    return this
  }
   
}

function isObject (variable) {
  return variable === Object(variable)
}

function isArray (variable) {
  return Array.isArray(variable)
}

function objToStr (obj) {
  if (typeof(obj) === 'string')
    return obj
  return obj.id || obj.label
}

function validaTamanhoCampoTexto (value, maxLength, nomeCampo='campo') {
  if (!value) 
    return
  if (value.length > maxLength)
    return `O ${ nomeCampo } deve ter até ${ maxLength } caracteres`
}

function mergeStrings (str1, str2) {
  if (!str1) 
    return str2
  if (!str2) 
    return str1
  return [str1, str2].join(' ')
}

Minuta.mergeMinutas = function (essaMinuta, outraMinuta) {
  const novaMinuta = new Minuta()
  novaMinuta.setProps({
    tipo: essaMinuta.tipo || outraMinuta.tipo,
    destinatarioDet: essaMinuta.destinatarioDet || outraMinuta.destinatarioDet,
    destinatariosCit: mergeArrays(essaMinuta.destinatariosCit, outraMinuta.destinatariosCit),
    rtm: mergeArrays(essaMinuta.rtm, outraMinuta.rtm),
    rtmClic: essaMinuta.rtmClic || outraMinuta.rtmClic,
    distribuicao: mergeArrays(essaMinuta.distribuicao, outraMinuta.distribuicao),
    indexadores: mergeArrays(essaMinuta.indexadores, outraMinuta.indexadores),
    indice: mergeStrings(essaMinuta.indice, outraMinuta.indice),
    carater: essaMinuta.carater || outraMinuta.carater,
    prioridade: essaMinuta.prioridade || outraMinuta.prioridade,
    referencias: mergeArrays(essaMinuta.referencias, outraMinuta.referencias),
    proposito: essaMinuta.proposito || outraMinuta.proposito,
    redator: essaMinuta.redator || outraMinuta.redator,
    resumo: mergeStrings(essaMinuta.resumo, outraMinuta.resumo),
    prazoSigilo: essaMinuta.prazoSigilo || outraMinuta.prazoSigilo,
    baseLegalSigilo: essaMinuta.baseLegalSigilo || outraMinuta.baseLegalSigilo,
    autoridade: essaMinuta.autoridade || outraMinuta.autoridade,
    cargo: essaMinuta.cargo || outraMinuta.cargo,
    razoes: mergeStrings(essaMinuta.razoes, outraMinuta.razoes),
    texto: mergeStrings(essaMinuta.texto, outraMinuta.texto),
  })
  return novaMinuta
}
   
Minuta.prepararArquivoExpedicao = prepararArquivoExpedicao
Minuta.prepararObjetoExpedicao = prepararObjetoExpedicao

export default Minuta
