import React from "react"
import PropTypes from 'prop-types'

import { Button } from '@blueprintjs/core'
import SubmenuPanel from './SubmenuPanel.js'
import DivWithSpinner from '../ui/DivWithSpinner.js'
import Spacer from '../ui/Spacer.js'
import SelectUnidades from '../ui/SelectUnidades.js'
import './Rubricas.css'

import { unidades } from '../lib/globalData.js'
import { carregarRubricas } from '../lib/minutaData.js'

function Rubrica (props) {

  const labelStatus = (decisao) => {
		const labels = {
			's': 'Solicitada',
			'a': 'Rubricado',
			'r': 'Rejeitado',
			'rs': 'Com ressalvas'
		}
		return labels[decisao]
	}
	
	const classeStatus = (decisao) => {
		const classes = {
			's': 'solicitada',
			'a': 'aprovado',
			'r': 'rejeitado',
			'rs': 'ressalvas'
		}
		return 'rubrica-status ' + classes[decisao]
	}
	
  return (
		<div className="rubrica">
			<span className="rubrica-unidade">{ props.rubrica.unidade }</span><span className={ classeStatus(props.rubrica.status) }>{ labelStatus(props.rubrica.status) }</span>	
			<div className="rubrica-comentarios">
			  <pre>
			    { props.rubrica.texto }
			  </pre>
			</div>
			<div className="rubrica-autor">
		 		<strong>{ props.rubrica.autor }</strong>
		 		<span> </span>
		 		<span style={{whiteSpace: 'nowrap'}}>
	 		    { props.rubrica.data }
	 		  </span>
			</div>
		</div>
  )
}

class Rubricas extends React.Component {
	constructor (props) {
		super ()
		this.state = {
			rubricas: [],
			unidadesSelecionadas: [],
			showSpinner: false,
		}
  }

  componentDidUpdate (prevProps) {
  	if (prevProps.minuta !== this.props.minuta) {
  		this.setState({
  			showSpinner: true
  		})
	    // Aqui nós simulamos que o sistema está carregando a lista
	    // de rubricas depois que uma minuta foi carregada.
	    // Não significa que o sistema vai funcionar assim -- talvez
	    // a lista de rubricas já possa vir junto com o resto da minuta
	    // quando ela for carregada? Isso vai depender da performance 
	    // do backend
	    carregarRubricas().then(rubricas => {
	     	this.setState({
	    		rubricas,
	    		showSpinner: false
	    	})
	    })
/*
			setTimeout(() => {
				this.setState({
					rubricas: rubricas,
					showSpinner: false
				})
			}, 5000)		
*/			
  	}
  	if (prevProps.show === this.props.show)
  		return
  	if (!this.props.show) 
  		return
  	this.setState({
		  // Nós tiramos a primeira distribuição na hora de mostrar a sugestão
		  // porque nunca se pede rubrica à unidade da primeira distribuição,
		  // que é a unidade que escreve e expede o desptel/circtel
  		unidadesSelecionadas: (this.props.minuta.distribuicao || []).slice(1).map(sigla => {
  			return unidades.find(unidade => unidade.label === sigla)  			
  		})
  	})
  }

  handleUnidadesChange (e) {
  	this.setState({
  		unidadesSelecionadas: e
  	})
  }  

  handleButtonClick (e) {
  	this.setState({
  		rubricas: [
  		  ...this.state.unidadesSelecionadas.map(item => {
  		  	  return {
  		  	  	unidade: item.label,
  		  	  	status: 's',
  		  	  	data: '14/11/2020 17:19'
  		  	  }
  		  }),
  		  ...this.state.rubricas,
  		],
  		unidadesSelecionadas: []
  	})
  }  

	render () {
    let rubricasHtml = this.state.rubricas.map((rubrica, index) => <Rubrica rubrica={rubrica} key={index} />)
    if (this.state.rubricas.length) {
			rubricasHtml.unshift(<h5 key={-1}>Rubricas solicitadas</h5>)
    }
		return (
			<SubmenuPanel title="Rubricas" onClick={this.props.onBack}>
			  <SelectUnidades 
			    label="Solicitar rubrica das unidades:"
			    unidades={unidades}
			    selected={this.state.unidadesSelecionadas}
			    onChange={this.handleUnidadesChange.bind(this)}
			  />
				<div style={{ marginTop: 8, textAlign: 'right' }}>
				  <Button rightIcon="arrow-right" intent="primary" onClick={this.handleButtonClick.bind(this)}>Solicitar rubricas</Button>
				</div>
			  <Spacer height={8} />
	      <DivWithSpinner spinner={ this.state.showSpinner }>
 		      { rubricasHtml }
        </DivWithSpinner>			  
			</SubmenuPanel>
	  )
	}
}

Rubricas.propTypes = {
	minuta: PropTypes.object.isRequired,
  show: PropTypes.bool.isRequired,
  onBack: PropTypes.func.isRequired
}

export default Rubricas