import React from 'react'
import PropTypes from 'prop-types'

import { Button, Icon } from '@blueprintjs/core'

import '../ui/CustomScroll.css'
import './SubmenuPanel.css'

function SubmenuPanel (props) {
	return (
    <div className="submenu-panel">
  		<div className="submenu-panel-title">
  		  <Button minimal={true} onClick={props.onClick}>
  			<Icon icon="chevron-left" />
  			</Button>
  			{ props.title }
  		</div>
  		<div className="submenu-panel-content custom-scroll">
  			{props.children}
  		</div>
    </div>
	)
}

SubmenuPanel.propTypes = {
  onClick: PropTypes.func.isRequired
}

export default SubmenuPanel