import React from 'react'
import PropTypes from 'prop-types'
import { Spinner } from '@blueprintjs/core'

function DivWithSpinner (props) {
	return (
		<div style={{
			position: 'relative',
			...props.style,
		}}>
		  <div style={{ 
		  	position: 'absolute',
		  	top: 0,
		  	left: 0,
		  	right: 0,
		  	width: '100%', 
		  	marginTop: props.spinnerTop || 32, 
		  	textAlign: 'center', 
		  	display: props.spinner ? 'block' : 'none' 
		  }}>
	      <Spinner size={Spinner.SIZE_STANDARD} />
	    </div> 
	    <div style={{
	    	opacity: props.spinner ? 0.5 : 1.0
	    }}>
	      { props.children }
	    </div>
	  </div>  
  )  
}

DivWithSpinner.propTypes = {
	spinner: PropTypes.bool.isRequired,
	spinnerTop: PropTypes.oneOfType([
	  PropTypes.number,
	  PropTypes.string
	]),
	style: PropTypes.object
}

export default DivWithSpinner