import React from 'react'
import PropTypes from 'prop-types'

import { Button, Classes, Dialog, Intent, TextArea } from '@blueprintjs/core'
import SubmenuPanel from './SubmenuPanel.js'
import DivWithSpinner from '../ui/DivWithSpinner.js'
import FormField from '../ui/FormField.js'
import Spacer from '../ui/Spacer.js'
import SelectUsuario from '../ui/SelectUsuario.js'

import AsyncSelect from '../ui/AsyncSelect.js';

import { AppToaster } from '../ui/toaster.js'


import { usuarios, buscarUsuarios } from '../lib/globalData.js'
import { adicionarDespacho, carregarDespachos } from '../lib/minutaData.js'

import './Despachos.css'

function removerAcentos (str = '') {
  return str.toString().normalize("NFD").replace(/[\u0300-\u036f]/g, "")
}

