function Despacho (props) {
  return (
    <div className="despacho">
      <div className="despacho-usuario">
        <span className="de-para">De</span> <b>{ props.origem }</b><br />
        <span className="de-para">Para</span> <b> { props.destino }</b><br />
        <span className="de-para">Em</span> { props.data }  
      </div>
      <pre>
      { props.texto }
      </pre>
    </div>  
  )
}

class Despachos extends React.Component {
  constructor (props) {
   	super()
   	this.state = {
      despachos: [],
   		usuarioDestino: null,
      textoDespacho: '',
      showDialog: '',
      showSpinner: false,
   	}
   	this.handleUsuarioChange = this.handleUsuarioChange.bind(this)
    this.handleDialogButtonClick = this.handleDialogButtonClick.bind(this)
    this.handleDialogClose = this.handleDialogClose.bind(this)
    this.handleTextareaChange = this.handleTextareaChange.bind(this)
  }

  componentDidUpdate (prevProps) {
    if (this.props.show && !prevProps.show) {
      this.setState({
        showSpinner: true
      })
      carregarDespachos()
      .then(data => this.setState({
        despachos: data,
        showSpinner: false
      }))
      .catch(error => {
        this.setState({
          showSpinner: false
        })
        AppToaster.show({
          message: 'Ocorreu um erro tentando carregar os despachos',
          intent: Intent.DANGER
        })
      })
    }
  }
  
  handleUsuarioChange (usuario) { 
  	this.setState({
  		usuarioDestino: usuario
  	})
  }	

  handleTextareaChange (e) {
    const texto = e.target.value
    this.setState({
      textoDespacho: texto
    })
  }

  handleButtonClick () {
    if (!this.state.usuarioDestino) return
    this.setState({
      showDialog: 'confirmacao'
    })
  }

  handleDialogClose () {
    this.setState({
      showDialog: ''
    })
  }

  handleDialogButtonClick (clickedOk) {
    return (e) => {
      this.setState({
        showDialog: ''
      })
      if (!clickedOk) 
        return
      const novoDespacho = {
        origem: 'fabio.cereda',
        destino: this.state.usuarioDestino.login,
        texto: this.state.textoDespacho,
        data: '14/11/2020 18:27'
      }
      /*
      this.setState({
        usuarioDestino: null,
        textoDespacho: '',          
      })
      */
      adicionarDespacho(novoDespacho)
      .then(data => {
        this.setState({
          showDialog: 'final'
        })
      })
      .catch(error => {
        AppToaster.show({
          message: 'Ocorreu um erro tentando enviar o despacho',
          intent: Intent.DANGER  
        })
      })
    }
  }

	render () {

		const despHtml = this.state.despachos.length ? (
      <>
			<h5>Despachos anteriores</h5>
			{ this.state.despachos.slice().reverse().map(despacho => <Despacho {...despacho} />) }
      </>
		) : ''	

    const usuarioDestino = this.state.usuarioDestino ? this.state.usuarioDestino.login : ''
		return (
			<SubmenuPanel title="Despachar" onClick={this.props.onBack}>
        <SelectUsuario
          message="Indique um usuário"
          label="Despachar para:"
          searchFunc={buscarUsuarios}
          usuarios={usuarios}
          usuario={this.state.usuarioDestino}
          selected={this.state.usuarioDestino}
          onChange={this.handleUsuarioChange}
        ></SelectUsuario>
      
			  <Spacer height={8} />

	      <FormField
	        label="Teor do despacho:"
	      >
		    <TextArea rows="3" fill={true} growVertically={true} value={this.state.textoDespacho} onChange={this.handleTextareaChange} />
 			  </FormField>   		 	

			  <div style={{ marginTop: 8, textAlign: 'right' }}>
			    <Button rightIcon="arrow-top-right" intent="primary" onClick={this.handleButtonClick.bind(this)}>Despachar</Button>
			  </div>
        <Spacer height={8} />
	      <DivWithSpinner spinner={ this.state.showSpinner }>
		    { despHtml }
        </DivWithSpinner>

        <Dialog isOpen={this.state.showDialog === 'confirmacao'} onClose={this.handleDialogClose}>
          <div className={Classes.DIALOG_BODY}>
            <p>Deseja despachar esta minuta? O acesso será transferido ao usuário <strong>{ usuarioDestino }</strong></p>
          </div>  
          <div className={Classes.DIALOG_FOOTER}>
            <div className={Classes.DIALOG_FOOTER_ACTIONS}>          
              <Button onClick={this.handleDialogButtonClick(false)}>Não, cancelar</Button>
              <Button onClick={this.handleDialogButtonClick(true)} intent={Intent.PRIMARY}>Sim, despachar</Button>
            </div>
          </div>  
        </Dialog>  

        <Dialog isOpen={this.state.showDialog === 'final'} onClose={this.handleDialogClose}>
          <div className={Classes.DIALOG_BODY}>
            <p>A minuta foi despachada para o usuário <strong>{ this.state.usuarioDestino && this.state.usuarioDestino.login }</strong>. Clique em <i>Fechar</i> para fechar esta janela.</p>
          </div>  
          <div className={Classes.DIALOG_FOOTER}>
            <div className={Classes.DIALOG_FOOTER_ACTIONS}>          
              <Button onClick={this.handleDialogClose} intent={Intent.PRIMARY}>Fechar</Button>
            </div>
          </div>  
        </Dialog>          

		  </SubmenuPanel>
		)  
	}
}

Despachos.propTypes = {
  show: PropTypes.bool.isRequired,
  onBack: PropTypes.func.isRequired
}

export default Despachos