import * as React from "react"
import PropTypes from 'prop-types'

import { Button } from '@blueprintjs/core'
import Spacer from '../ui/Spacer.js'
import SubmenuPanel from './SubmenuPanel.js'
import Minuta from '../Minuta.js'
import './Expedir.css'



class Expedir extends React.Component {
	constructor (props) {
		super ()
		this.state = {
			errors: []
		}
  }

  componentDidUpdate (prevProps) {
    const INTERVALO_VERIFICAR = 2500
    const timeoutVerificar = function () {
  		this.verificarMinuta()
  		if (this.props.show)
  			setTimeout(timeoutVerificar, INTERVALO_VERIFICAR) 
    }.bind(this) 

  	if (this.props.show && !prevProps.show) {
  		this.verificarMinuta()	
  		setTimeout(timeoutVerificar, INTERVALO_VERIFICAR)
  	}
  }

  verificarMinuta () {
		const errors = this.props.minuta.validarParaExpedir()
		this.setState({
			errors: function () {
				const errorsArray = []
				for (let prop in errors) {
					errorsArray.push(errors[prop])
				}
				return errorsArray
			}()
		})
		return !(errors && (errors.length > 0))
  }

  handleButtonClick (e) {
  	// Temos que verificar a minuta quando o usuário clica no botão
  	// expedir, pois ele pode ter introduzido algum erro na minuta
  	// depois que abriu o slider Expedir
    if (this.verificarMinuta()) {
    	const arquivoExpedicao = Minuta.prepararArquivoExpedicao(this.props.minuta)
    	const objetoExpedicao = Minuta.prepararObjetoExpedicao(this.props.minuta)
    	console.log(arquivoExpedicao)
    	console.log(objetoExpedicao)

  	  alert(arquivoExpedicao)
    }
  }  

	render () {
		let corpoTexto 
		let hasErrors = this.state.errors && this.state.errors.length
		if (hasErrors) {
			corpoTexto = (
				<div className="expedicao-corpo-texto">
				  Esta minuta contém alguns erros que impedem sua expedição:
				<ul>
				  { this.state.errors.map(error => <li>{error}</li>) }
			  </ul>
          Corrija estes erros e tente expedir novamente.
        </div>  
			)
		}
		else {
			corpoTexto = (
			  <div className="expedicao-corpo-texto">
			    Este expediente será transmitido e seu nome ficará registrado. Confirma a expedição?
			  </div>
			)
		}

		return (
			<SubmenuPanel title="Expedir" onClick={this.props.onBack}>

			  { corpoTexto }

				<Spacer height={16} />
				
				<div style={{ marginTop: 8, textAlign: 'right' }}>
			  	<Button icon="chevron-left" onClick={this.props.onBack}>Cancelar</Button>
			  	&nbsp;&nbsp;
				  <Button rightIcon="direction-right" intent="success" disabled={hasErrors} onClick={this.handleButtonClick.bind(this)}>Expedir</Button>
				</div>
			  <Spacer height={8} />
			</SubmenuPanel>
	  )
	}
}

Expedir.propTypes = {
	minuta: PropTypes.instanceOf(Minuta),
  show: PropTypes.bool.isRequired,
  onBack: PropTypes.func.isRequired
}

export default Expedir