import React from 'react'
import PropTypes from 'prop-types'

import { MenuItem, Intent } from '@blueprintjs/core'
import { MultiSelect } from '@blueprintjs/select'
import FormField from './FormField.js'

class SelectUnidades extends React.Component {

  constructor (props) {
    super()
   	this.state = {
   		unidades: Array.isArray(props.selected) ? props.selected : []
   	}
   	this.filterItems = this.filterItems.bind(this)
   	this.getTagProps = this.getTagProps.bind(this)
   	this.renderItem = this.renderItem.bind(this)
   	this.renderTag = this.renderTag.bind(this)
   	this.handleItemSelect = this.handleItemSelect.bind(this)  	
   	this.handleTagRemove = this.handleTagRemove.bind(this)
  }

  componentDidUpdate (prevProps) {
  	if (this.props.selected === this.state.unidades)
  		return
  	this.setState({
  		unidades: this.props.selected
  	})
  }

  renderSelectedItem (item) {
  	if (!item) return ''
    const { label, text } = item		
  	const txt = `${ label } - ${ text }`
  	return txt
  }  

  renderItem (item, { modifiers, handleClick }) {
    if (!modifiers.matchesPredicate) {
      return null;
    }
  	const { label, text } = item
    return (
      <MenuItem
        active={modifiers.active}
        key={ label }
        label={ label }
        text={ text }
        onClick={ handleClick }
        shouldDismissPopover={false}
      />
    )
  }

  renderTag (item) {
    if (typeof(item) == 'string')
    	return <div>{ item }</div>
    return <div title={item.text}>{ item.label || item.text }</div>
  }  
  
  filterItems (searchStr) {
    searchStr = searchStr.toLowerCase()
  	return this.props.unidades.filter(item => {
      if (this.state.unidades.indexOf(item) >= 0)
        return false
  		return item.label.toLowerCase().indexOf(searchStr) >= 0 ||
  		       item.text.toLowerCase().indexOf(searchStr) >= 0
  	})
  }

  getTagProps (value, index) {
  	return {
  		interactive: true,
  		intent: Intent.NONE,
  	}
  }  
  
  handleItemSelect (item) { 
  	const unidades = this.state.unidades
  	unidades.push(item)
  	this.setState({
  		unidades 
  	})
  	if (this.props.onChange) {
  		this.props.onChange(unidades)
  	}
  }	

  handleTagRemove (selectedItems, selectedIndex) {
  	let itemToRemove = this.state.unidades[selectedIndex]
  	let unidades = this.state.unidades.filter(unidade => unidade !== itemToRemove)
  	this.setState({
  		unidades
  	})
    if (this.props.onChange) {
      this.props.onChange(unidades)
    }  	
  }  

  render () {
    return (
			<FormField
	      message={this.props.message}
	      label={this.props.label || 'Usuário:'}
	    >
		    <MultiSelect
		      fill={true}
			    initialContent={null}		      
		      items={this.props.unidades}			
	        itemListPredicate={this.filterItems}		
		      itemRenderer={ this.renderItem }
		      onItemSelect={ this.handleItemSelect }
		      openOnKeyDown={true}
		      popoverProps={{ 
		      	minimal: true,
		      	position: 'bottom-right' 
		      }}
		      resetOnSelect={true}
		      selectedItems={ this.state.unidades }
		      tagRenderer={ this.renderTag }		  
				  tagInputProps={{
				  	onRemove: this.handleTagRemove,
				  	tagProps: this.getTagProps,
	  		  }}
		    ></MultiSelect>  
		   </FormField>    	
    )
  }
}

SelectUnidades.propTypes = {
  label: PropTypes.string,
  message: PropTypes.string,
  onChange: PropTypes.func,
  selected: PropTypes.array,
  unidades: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.func
  ]).isRequired,
}

export default SelectUnidades





