const { parse } = require('node-html-parser')

function inserirProposito (html, proposito) {
  if (!proposito) return html
  return html.replace(/<p(.*?)>/, (fullMatch, segmentMatch) => {
    return  `<p${ segmentMatch }><span class="proposito">${ proposito }</span>`
  })
}

function validar (html) {
  const root = parse(html)
  
  function checkNode (node) {
    if (node.rawTagName === 'script') 
      throw Error('<script> tag found')
    if (node.rawAttrs && node.rawAttrs.match(/on(\S+)(\s*)=/g)) 
      throw Error(`JS events found inside tag <${ node.rawTagName }>`)
    if (node.childNodes)
      node.childNodes.forEach(child => checkNode(child))
  }      
  
  try {
    checkNode(root)
    return true
  }  
  catch (error) {
    console.error(error)
    return false
  }  
}  

export default {
	inserirProposito,
	validar,
}
