import React from 'react'
import PropTypes from 'prop-types'

import AsyncSelect from './AsyncSelect.js'
import FormField from './FormField.js'

import { buscarUsuarios } from '../lib/globalData.js'


const itemRenderer = ({ nome, unidade }) => {
  return (
    <div style={{ display: 'flex ', flexDirection: 'column'}}>
      <div style={{ flex: 1 }}>{ nome }</div>
      <div style={{ opacity: 0.9, fontSize: '0.9em', paddingTop: 2 }}>{ unidade }</div>
    </div>
  ) 
}

const valueRenderer = (item) => {
  return item.nome + ' (' + item.unidade + ')'
}


class SelectUsuario extends React.Component {

  constructor (props) {
    super()
   	this.state = {
   		usuario: props.selected,
  	}
   	this.handleItemSelect = this.handleItemSelect.bind(this)  	
  }

  handleItemSelect (item) { 
  	this.setState({
  		usuario: item 
  	})
  	if (this.props.onChange)
  		this.props.onChange(item)
  }	

  render () {
    return (
			<FormField
	      message={this.props.message}
	      label={this.props.label || 'Usuário:'}
	    >
        <AsyncSelect 
          fill={true} 
          maxMenuHeight={314}
          placeholder="Procurar..."
          loadingMessage='Procurando...'
          typeToSearchMessage="Digite para procurar"
          noOptionsMessage="Nenhum usuário encontrado"
          itemRenderer={itemRenderer}
          valueRenderer={valueRenderer}
          searchItems={buscarUsuarios}
          onItemSelect={this.handleItemSelect}
        />      
		   </FormField>    	
    )
  }
}

SelectUsuario.propTypes = {
  label: PropTypes.string,
  message: PropTypes.string,
  onChange: PropTypes.func,
  selected: PropTypes.oneOfType([
    PropTypes.object,
    PropTypes.string
  ]),  
  usuarios: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.func
  ]).isRequired,
}

export default SelectUsuario





