function mergeArrays (arr1, arr2) {
  if (!Array.isArray(arr1)) return arr2
  if (!Array.isArray(arr2)) return arr1
  const newArr = [...arr1]
  arr2.forEach(item => {
    if (newArr.indexOf(item) < 0) {
      newArr.push(item)
    }  
  })  
  return newArr
}  

function getMinutaId () {
  const location = global ? global.location : window.location
  let locationSearch = location.search.substr(1, location.search.length - 1)
  if (locationSearch)
    return locationSearch
  return 0
}

function firstCharToLowerCase (str) {
  return str.charAt(0).toLowerCase() + str.substr(1)
}

export {
  getMinutaId,
	mergeArrays,
  firstCharToLowerCase
}