import React from 'react'
import { Button, Dialog, Classes, InputGroup, Intent, Spinner } from '@blueprintjs/core'
import FlexDiv from '../ui/FlexDiv.js'
import Spacer from '../ui/Spacer.js'
import { AppToaster } from '../ui/toaster.js'
import ListaMinutas from './ListaMinutas.js'
import PainelMinuta from './Painel-Minuta.js'

class DialogMinutas extends React.Component {
	constructor (props) {
	  super()
	  this.state = {
	  	isOpen: props.show,
	    grupos: [],
	    minutas: [],
	    filtro: '',
	    itemSelecionado: null,
	    showListaSpinner: false,
	    showSpinner: false,
	    modelo: null
	  }

	  this.handleClose = this.handleClose.bind(this)
	  this.handleOk = this.handleOk.bind(this)
	  this.handleFilterChange = this.handleFilterChange.bind(this)
	  this.handleSelectMinuta = this.handleSelectMinuta.bind(this)
	}

  componentDidUpdate (prevProps) {
  	if (this.props.show === prevProps.show) 
  		return
  	if (!this.props.show)
  		return

  	//Se chegar aqui, é porque this.props.show == true e prevProps.show == false
  	if (typeof this.props.grupos === 'function') {
  		// Se this.props.grupos for uma função, 
  		// deverá retornar uma promise para passar
  		// o array de grupos
  		this.setState({
  			showListaSpinner: true
  		})
  		this.props.grupos()
  		.then(data => {
  			this.setState({	
  				grupos: data,
  				showListaSpinner: false
  			})
  		})
  		.catch(error => this.setState({
  			showListaSpinner: false
  		}))
  	}
  	else {
  		// Se this.props.grupos for um Array,
  		// ele é incorporado ao state
  		if (Array.isArray(this.props.grupos)) {
  			this.setState({
  				grupos: this.props.grupos
  			})
  		}	
  	}

  	if (typeof this.props.minutas === 'function') {
  		// Se this.props.minutas for uma função, 
  		// deverá retornar uma promise que passará  
  		// o array de minutas
  		this.setState({
  			showListaSpinner: true
  		})
  		this.props.minutas()
  		.then(data => {
  			this.setState({
  				minutas: data,
  				showListaSpinner: false
  			})
  		})
  		.catch(error => this.setState({
  			showListaSpinner: false
  		}))	
  	}  	
  	else {
  		// Se this.props.minutas for um Array,
  		// ele é incorporado ao state 
  		if (Array.isArray(this.props.minutas)) {
  			this.setState({
  				minutas: this.props.minutas
  			})
  		}
  	}
  }

	handleClose (e) {
		this.props.onClose(e)
	}

	handleOk (handler) {
		return (e) => 	{
			if (handler && (handler(this.state.modelo) === false)) 
				return
		  this.props.onClose(e)
		}  
	}

	handleSelectMinuta (itemSelecionado) {
		if (itemSelecionado === this.state.itemSelecionado)
			return
		if (this.props.minuta && typeof this.props.minuta === 'function') {
			this.setState({
				showSpinner: true,
			})			
			this.props.minuta()
			.then(data => {
				this.setState({
					modelo: data,
					itemSelecionado,
					showSpinner: false
				})
			})
			.catch(error => {
				AppToaster.show({
					message: error || 'Erro ao tentar carregar este documento',
					intent: Intent.DANGER
				})
				this.setState({
					showSpinner: false
				})
			})
		}
	}

  handleFilterChange (e)  {
  	this.setState({
  		filtro: e.target.value
  	})
  }

  filtrarMinutas (grupos, minutas) {
  	const filtro = this.state.filtro.toLowerCase()
  	const filterFunction = (minuta) => {
 			if (typeof minuta !== 'string')
 				minuta = (minuta.title || minuta.text || '') + minuta.label
  		return minuta.toLowerCase().indexOf(filtro) >= 0
  	}	

  	if (!grupos || !grupos.length) {
  		return [{
  			nome: '',
  			minutas: (minutas || []).filter(filterFunction)
  		}]	
  	}	
  	return grupos.map(grupo => {
  		return {
  			nome: grupo.nome,
  			minutas: grupo.minutas.filter(filterFunction)
  		}
  	})
  }

  render () {
  	const ButtonStyle = { minWidth: 100 }
  	const spinnerStyle = { 
			position: 'sticky', 
			top: 200, 
			left: 0,
			right: 0,
			height: 0,
			textAlign: 'center',
			zIndex: 100        
		}

  	const actionButtons = this.props.actions.map((action, index) => {
  		return <Button 
  		  fill={false} key={index}
  		  intent={action.intent} 
  		  style={action.style || ButtonStyle} 
  		  onClick={this.handleOk(action.handler)}>{ action.text || action.label }</Button>
  	})

    return (
			<Dialog
			  style={{ width: 900 }}
	      onClose={this.handleClose}
	      title={this.props.title || 'Minutas'}
	      isOpen={this.props.show}
				canOutsideClickClose={false}	      
 	    >
	      <div className={Classes.DIALOG_BODY}>
          
          <FlexDiv style={{ height: 480 }}>

 						<FlexDiv direction="vertical" style={{ width: 400 }}>
			        <InputGroup
					      asyncControl={true}
					      leftIcon="search"
					      placeholder="Filtrar..."
					      onChange={this.handleFilterChange}
					      value={this.state.filtro}
					    />	
			        <Spacer height={8} />			    

			        <div style={{ flex: 1, overflowY: 'auto', border: '1px solid #aaa', backgroundColor: '#ffffff' }}>
				        <div style={{
				        	...spinnerStyle,
				        	display: this.state.showListaSpinner ? 'block' : 'none'
				        }}>
	        				<Spinner size={Spinner.SIZE_STANDARD} />
	      				</div>  			        
			          <ListaMinutas 
			            grupos={this.filtrarMinutas(this.state.grupos, this.state.minutas)} 
			            onClick={this.handleSelectMinuta}
			          />
			        </div>  
			      </FlexDiv>
			      
			      <div style={{ position: 'relative', width: 640, padding: 16, paddingTop: 0, marginLeft: 16, overflowY: 'auto', overflowX: 'hidden', backgroundColor: 'white', border: '1px solid #aaa' }}>
			        <div style={{
			        	...spinnerStyle,
			        	display: this.state.showSpinner ? 'block' : 'none'
			        }}>
        				<Spinner size={Spinner.SIZE_STANDARD} />
      				</div>  
			        <PainelMinuta 
			          minuta={ this.state.modelo } 
			          placeholder={ this.props.placeholder }
			          style={{ opacity: this.state.showSpinner ? 0.4 : 1 }}/>
			      </div>    

			    </FlexDiv>

	      </div>  
        <div className={Classes.DIALOG_FOOTER}>
          <div className={Classes.DIALOG_FOOTER_ACTIONS}>	        
	          <Button fill={false} style={ButtonStyle} onClick={ this.props.onClose }>Cancelar</Button>          
	          {actionButtons}
	        </div>  
	      </div>
	    
	    </Dialog>  
    )
  }

}

export default DialogMinutas
