import React from 'react'
import PropTypes from 'prop-types'

import { Tab, Tabs } from '@blueprintjs/core'
import TabsMascara from './TabsMascara.js'
import './Mascara.css'

import Minuta from './Minuta.js'

class MetadataPanel extends React.PureComponent {
  constructor (props) {
    super()
    this.state = {
      selectedTab: 'cabecalho'
    }
    this.handleTabChange = this.handleTabChange.bind(this)
  }  

  handleTabChange (newTab) {
    this.setState({
      selectedTab: newTab
    })  
  }

  render () {
    return (
      <div className="mascara-panel">
               
        <Tabs
          className="mascara-tab"
          animate={true}
          horizontal={true}
          fill={true}
          onChange={this.handleTabChange}
        >
          <Tab id="cabecalho" title="Cabeçalho"/>
          <Tab id="resumo" title="Resumo"/>
          <Tab id="tci" title="TCI"/>
          <Tab id="anexos" title="Anexos"/>
        </Tabs>
        
        <TabsMascara selectedTab={this.state.selectedTab} minuta={this.props.minuta} />

      </div>
    )
  }  
}

MetadataPanel.propTypes = {
  minuta: PropTypes.instanceOf(Minuta).isRequired
}

export default MetadataPanel
