import React from 'react'
import PropTypes from 'prop-types'

import { MenuItem, Intent, Icon } from '@blueprintjs/core'
import { MultiSelect, Suggest } from '@blueprintjs/select'

import FloatingField from './FloatingField.js'

function removerAcentos (str) {
  return str.normalize("NFD").replace(/[\u0300-\u036f]/g, "")
}

class FloatingSelect extends React.PureComponent {
  constructor (props) {
  	super()
  	this.state = {
      separator: props.separator || '/',
  		value: props.value || (props.multiple ? [] : null),
      query: '',
  		notEmpty: false,
      error: ''
  	}
  	this.selectRef = React.createRef()
    this.createNewItemRenderer = this.createNewItemRenderer.bind(this)        
  	this.filterItems = this.filterItems.bind(this)
    this.getItemsNotSelected = this.getItemsNotSelected.bind(this)
  	this.getTagProps = this.getTagProps.bind(this)
    this.handleBlur = this.handleBlur.bind(this)
    this.handleClearOption = this.handleClearOption.bind(this)
  	this.handleFieldClick = this.handleFieldClick.bind(this)
  	this.handleSelect = this.handleSelect.bind(this)
  	this.handleSelectSimple = this.handleSelectSimple.bind(this)
  	this.handleTagRemove = this.handleTagRemove.bind(this)
    this.handleTagInputKeyUp = this.handleTagInputKeyUp.bind(this)
    this.renderItem = this.renderItem.bind(this)
  }

  componentDidMount () {
  	const inputElement = this.selectRef.current.inputEl
  	if (!inputElement) return
    inputElement.placeholder = this.props.placeholder || ''
  }

  componentDidUpdate (prevProps) {
    if (this.props.value === prevProps.value) 
      return
    this.setState({
      value: this.props.value || (this.props.multiple ? [] : null),
    })
  }

  getTagProps (value, index) {
  	return {
  		interactive: true,
  		intent: this.props.highlightFirstTag && index === 0 ? Intent.PRIMARY : Intent.NONE,
  	}
  }

  getItemsFromQuery (query, items) {
    const queryTerms = query.toUpperCase().split(/[/\-,;]/)
    if (queryTerms.length < 1) return []
    let matchingItems = []
    for (let i = 0; i < queryTerms.length; i++) {
      let thisTerm = queryTerms[i].trim()
      if (!thisTerm) continue
      let matchingItem = items.find(item => item.label === thisTerm)
      if (matchingItem)
        matchingItems.push(matchingItem)
      else
        throw Error('Item não encontrado: ' + thisTerm)
    }
    return matchingItems
  }

  addItemsFromQuery (query, isKeyup) {
    let newItems
    try {
      newItems = this.getItemsFromQuery(query, this.props.items)
      if (isKeyup && newItems.length === 1) return false
    }
    catch (err) {
      return false
    }
    // Não podemos repetir itens!
    newItems = newItems.filter(item => this.state.value.indexOf(item) < 0)
    newItems.forEach(item => this.state.value.push(item))
    this.setState({
      value: this.state.value,
      errors: {
        distribuicao: ''
      }
    })
    if (this.props.onChange) {
      this.props.onChange(this.state.value)
    }    
    return true
  }

  createNewItemRenderer (query, active, handleClick) {
    try {
      let possibleItems = this.getItemsFromQuery(query, this.props.items)
      if (possibleItems.length === 1) return null
      let str = 'Adicionar ' + possibleItems.map(item => item.label).join(this.state.separator)
      return <MenuItem text={ str } />
    } catch {
      return null
    }  
  }


  handleFieldClick () {
  	if (!this.selectRef || !this.selectRef.current || !this.selectRef.current.input) return
   	this.selectRef.current.input.focus()
  }

  handleBlur (e) {
    let target = e.target
    setTimeout(() => {
      let query = target.value
      if (!query) return
      this.addItemsFromQuery(query, false)
      this.setState({
        query: ' '  
      })
    }, 100)

  }

  renderItem (item, { modifiers, handleClick }) {
    if (!modifiers.matchesPredicate) {
      return null;
    }
  	let key, label, text	
  	if (typeof(item) == 'string') {
  		key = item
  		text = item
  		label = ''	
  	}
  	else {
  		key = item.id
  		text = item.text
  		label = item.label
  	}    
    return (
      <MenuItem
        active={modifiers.active}
        key={ key }
        label={ label }
        text={ text }
        onClick={ handleClick }
        shouldDismissPopover={false}
      />
    )
  }

  renderTag (item) {
    if (typeof(item) == 'string')
      return <div>{ item }</div>
    return <div title={item ? item.text : ''}>{ item ? item.label || item.text : ''}</div>
  }    

  renderInputValue (item) {
  	if (typeof (item) == 'string')
  		return item
  	return item.label || item.text || item.id
  }

  handleTagRemove (selectedItems, selectedIndex) {
  	let itemToRemove = this.state.value[selectedIndex]
  	let value = this.deselectItem(itemToRemove)
    if (this.props.onChange) {
      this.props.onChange(value)
    }  	
  }

	isItemSelected (item) {
  	return this.state.value.indexOf(item) >= 0
  }

  selectItem (item) {
  	const value = [...this.state.value, item]
  	this.setState({
  		value
  	})
  	return value
  }

  deselectItem (item) {
  	const value = this.state.value.filter(thisItem => thisItem !== item)
  	this.setState({
  		value 
  	})
  	return value
  }

  /****

  filterItems() recebe uma string contendo a query digitada pelo usuário,
  e retorna um array de items que atendem à query. 
  O array retornado é ordenado pelo seguinte critério:
  - se a query corresponde ao começo do label (sigla) de um item, ele 
    aparece no topo da lista; 
  - os itens que contenham a query no label (sigla) ou no text (descrição)
    aparecem a seguir.
  Os itens de cada grupo acima serão ordenados alfabeticamente pelo text.

  ***/

  filterItems (str) {
  	str = removerAcentos(str).toUpperCase().trim()
    if (!str)
      return this.props.items
    const itemsRank = this.props.items.map(item => {
      let label, text = ''
      if (typeof item == 'string')
        label = item
      else {
        label = item.label || ''
        text = item.text || '' 
      }      
      let index = label.toUpperCase().indexOf(str)
      if (index >= 0) {
        if (index > 0) 
          index = 1
        return {
          item,
          text,
          index
        }
      }
      index = removerAcentos(text).toUpperCase().indexOf(str)
      if (index >= 0)
        index = 1
      else
        index = -1
      return {
        item,
        text,
        index
      }
    })
    let filteredItems = itemsRank.filter(rankedItem => rankedItem.index >= 0)
    let selectedItems = this.props.multiple ? (this.state.value || []) : [this.state.value]
    filteredItems = filteredItems.filter(rankedItem => {
      return selectedItems.indexOf(rankedItem.item) < 0
    })
    const sortedItems = filteredItems.sort((a, b) => {
      if (a.index != b.index)
        return a.index - b.index
      if (a.text < b.text)
        return -1
      return 1
    })
    return sortedItems.map(rankedItem => rankedItem.item)
  }

  filterItem (str, item) {
  	str = str.toUpperCase()
  	return (item.id + item.label + item.text).toUpperCase().indexOf(str) >= 0
  }

  getItemsNotSelected (items) {
    return items.filter(item => this.state.value.indexOf(item) < 0)
  }

  handleQueryChange (e) {
    this.setState({
      query: e
    })
  }
  
  handleSelect (item) {
  	let value
    if (!this.isItemSelected(item)) {
      value = this.selectItem(item)
    } 
    else {
      value = this.deselectItem(item)
    }
    if (this.props.onChange) {
      this.props.onChange(value)
    }
  }

  handleSelectSimple (item) {
  	this.setState({
  		value: item
  	})
    if (this.props.onChange) {
      this.props.onChange(item)
    }  	
  }

  handleClearOption () {
    this.handleSelectSimple(null)
  }
 
  handleTagInputKeyUp (e) {
    if (this.state.error) {
      this.setState({
        error: ''
      })
    }    
    let query = e.target.value
    if (e.key === 'Enter') {
      if (this.addItemsFromQuery(query, true)) {
        this.setState({
          query: ''  
        })
      }
    }
  }

  render = () => {
    /* 
    const hasItems = this.props.items && this.props.items.length
    const noResults = hasItems ? this.props.noResults || <MenuItem disabled={true} text="No results." /> : null
    */
    const noResults = this.props.noResults || null

    let isEmpty = true
    if (this.props.multiple)
      isEmpty = this.state.value.length === 0
    else
      isEmpty = !this.state.value
    const component = this.props.multiple ? (
			<MultiSelect
			  className="embedded"
			  placeholder={this.props.placeholder || ''}
        disabled={this.props.disabled}
			  ref={this.selectRef}
			  fill={true}
			  createNewItemFromQuery={this.props.createNewItemFromQuery}
        createNewItemRenderer={this.createNewItemRenderer}
			  initialContent={null}
			  items={this.getItemsNotSelected(this.props.items)}	
			  itemListPredicate={this.props.filterItems || this.filterItems}
			  itemRenderer={this.renderItem}
			  openOnKeyDown={true}
        query={this.state.query}
			  noResults={ noResults }
			  onItemSelect={this.handleSelect}
			  onItemsPaste={this.handleItemsPaste}

        onQueryChange={this.handleQueryChange.bind(this)}

			  popoverProps={{ minimal: true }}
			  resetOnSelect={true}
			  tagRenderer={ this.props.renderTag || this.renderTag }
			  tagInputProps={{
			  	onRemove: this.handleTagRemove,
			  	tagProps: this.getTagProps,
          inputProps: {
            onKeyUp: this.handleTagInputKeyUp
          },
  		  }}
			  selectedItems={this.state.value}
			></MultiSelect>  
		) : (
      <>
      <Suggest
	      className="embedded suggest"
	      defaultSelectedItem={this.props.defaultSelectedItem}
        placeholder={this.state.selectedItem ? this.props.placeholder : ''}
        disabled={this.props.disabled}
	      ref={this.selectRef}
	      fill={true}
	      items={this.props.items}			
	      itemListPredicate={this.filterItems}
	      itemRenderer={ this.renderItem }
	      openOnKeyDown={true}
	      noResults={ noResults }
	      onItemSelect={this.handleSelectSimple}
	      onItemsPaste={this.handleItemsPaste}
	      popoverProps={{ minimal: true }}
	      resetOnSelect={true}
	      inputValueRenderer={ this.props.renderInputValue || this.renderInputValue }
        selectedItem={this.props.value}
	    ></Suggest>  
      <Icon icon="cross" style={{ opacity: this.state.value ? 0.75 : 0 }} onClick={this.handleClearOption} />
      </>
		)
 
 		return (
			<FloatingField
			  className={ isEmpty && !this.props.placeholder ? 'empty' : 'not-empty' }
			  onClick={this.handleFieldClick}
        onBlur={this.handleBlur}
			  { ...this.props }
        error={ this.props.error || this.state.error }
      >	
			  { component }	
			</FloatingField>	  	
	  )
  }
}

FloatingSelect.propTypes = {
  createNewItemFromQuery: PropTypes.func,
  defaultSelectedItem: PropTypes.object,  
  error: PropTypes.string,
  highlightFirstTag: PropTypes.bool,  
  items: PropTypes.array,
  multiple: PropTypes.bool,
  noResults: PropTypes.node,  
  placeholder: PropTypes.string,
  separator: PropTypes.string,
  value: PropTypes.oneOfType([
    PropTypes.array,
    PropTypes.object
  ]),
  
  filterItems: PropTypes.func,  
  onChange: PropTypes.func,  
  renderTag: PropTypes.func,
  renderInputValue: PropTypes.func
}

export default FloatingSelect