import React from 'react'
import PropTypes from 'prop-types'
import { Button, Dialog, Classes, Intent, InputGroup, Label } from '@blueprintjs/core';

class DialogInputNome extends React.Component {
	constructor (props) {
	  super()
	  this.state = {
	  	value: ''
	  }
	  this.handleInputChange = this.handleInputChange.bind(this)
	  this.handleClickOk = this.handleClickOk.bind(this)
	  this.handleClose = this.handleClose.bind(this)
	}

	componentDidUpdate (prevProps) {
		if (this.props.show && !prevProps.show) {
			this.setState({
				value: ''
			})
		}
	}

	handleInputChange (e) {
		this.setState({
			value: e.target.value
		})
	}

	handleClickOk (e) {
		this.props.onChange && this.props.onChange(this.state.value)
		this.handleClose(e)
	}

	handleClose (e) {
		this.props.onClose && this.props.onClose() 
	}	

  render () {
		const ButtonStyle = { minWidth: 100 }
		return (
			<Dialog
			  style={{ width: this.props.width || 480 }} 
	      onClose={this.handleClose}
	      title={this.props.title || 'Salvar como'}
	      isOpen={this.props.show}
	    >
	      <div className={Classes.DIALOG_BODY}>
	        <Label>
	          { this.props.label || 'Indique um nome para este documento:' }
	        </Label>  
		  		<InputGroup
			      asyncControl={true}
			      placeholder={this.props.placeholder}
			      onChange={this.handleInputChange}
			      value={this.state.value}
			    />	
			  </div>
		    <div className={Classes.DIALOG_FOOTER}>
	        <div className={Classes.DIALOG_FOOTER_ACTIONS}>	        
	          <Button fill={false} style={ButtonStyle} onClick={ this.props.onClose }>Cancelar</Button>
	          <Button 
	            fill={false} 
	            style={ButtonStyle} 
	            type="submit" 
	            intent={Intent.PRIMARY} 
	            disabled={!this.state.value}
	            onClick={ this.handleClickOk }>{ this.props.okButtonLabel || 'OK' }</Button>
	        </div>  
	      </div> 
	    </Dialog>
    )   
	}
}

DialogInputNome.propTypes = {
	label: PropTypes.string,
	okButtonLabel: PropTypes.string,
	placeholder: PropTypes.string,
	show: PropTypes.bool.isRequired,
	title: PropTypes.string,
	width: PropTypes.oneOfType([
		PropTypes.number,
		PropTypes.string
	]),
	onChange: PropTypes.func.isRequired,
	onClose: PropTypes.func.isRequired,
}

export default DialogInputNome