import React from 'react'
import PropTypes from 'prop-types'

import './TabsMascara.css'

import { Alignment, Button, ButtonGroup, MenuItem } from "@blueprintjs/core";
import { FloatingInput, FloatingSelect, FloatingNativeSelect, FloatingTextarea } from './floating'
import FlexDiv from './ui/FlexDiv.js'
import Spacer from './ui/Spacer.js'
import Minuta from './Minuta.js'
import { 
	unidades, 
	indexadores, 
	postos,
	grausSigilo, 
  grupos,
	prazos, 
	basesLegais, 
	prioridades, 
	propositos } from './lib/globalData.js'


function stringArrayToItemArray (strArr, items) {
  if (!items) {
  	return strArr.map(id => {
  		return { 
  		  id,
  		  label: id,
  		  text: id
  		}
  	})
  }	
  return strArr.map(id => items.find(item => (item.id || item.label) === id)).filter(item => Boolean(item))
}

function converterMinuta (minuta) {
	if (!minuta) 
		throw Error ('Constructor de converterMinuta deve receber um objeto Minuta como parâmetro')
  const minutaConvertida = {
		tipo: minuta.tipo,  		
		destinatarioDet: postos.find(posto => posto.id === minuta.destinatarioDet),
		destinatariosCit: stringArrayToItemArray(minuta.destinatariosCit, postos),
		rtm: stringArrayToItemArray(minuta.rtm, postos),
		distribuicao: stringArrayToItemArray(minuta.distribuicao, unidades),   
		indexadores: stringArrayToItemArray(minuta.indexadores, indexadores),
		indice: minuta.indice,
		carater: minuta.carater,
		prioridade: minuta.prioridade,
		referencias: stringArrayToItemArray(minuta.referencias),
		proposito: minuta.proposito,
		redator: minuta.redator,
		resumo: minuta.resumo,
		prazoSigilo: minuta.prazoSigilo,
		baseLegalSigilo: minuta.baseLegalSigilo,
		autoridade: minuta.autoridade,
		cargo: minuta.cargo,
    razoes: minuta.razoes	
  } 
  return minutaConvertida   
}


class Cabecalho extends React.Component {

  constructor (props) {
  	super()

  	const minutaConvertida = converterMinuta(props.minuta)
  	this.state = {
  		errors: {},
  		minuta: props.minuta,
  		...minutaConvertida,
  	}

  	this.createNewReferenciaFromQuery = this.createNewReferenciaFromQuery.bind(this)
  	this.minutaEClassificada = this.minutaEClassificada.bind(this)
  	this.handleClickTipo = this.handleClickTipo.bind(this)
  	this.handleButtonClick = this.handleButtonClick.bind(this)
		this.handleIndiceChange = this.handleIndiceChange.bind(this)
  	this.handleResumoChange = this.handleResumoChange.bind(this)
  	this.handleRazoesChange = this.handleRazoesChange.bind(this)
  	this.handleChange = this.handleChange.bind(this)
  	this.handleBlurRedator = this.handleBlurRedator.bind(this)
  }

  componentDidUpdate(prevProps) {
  	if (prevProps.minuta === this.props.minuta) return
  	const minutaConvertida = converterMinuta(this.props.minuta)	
  	this.setState({
  		minuta: this.props.minuta,
  		...minutaConvertida,
  	})
  } 

  createNewReferenciaFromQuery (query) {
  	query = query.toUpperCase()
		const regex = /^(TEL|DET|DESPTEL|CIT|CIRCTEL)\s+((\d+)(\/(\d+))*)$/
		const matches = query.match(regex)
		if (!matches)
			return null
		let anoCorrente = (new Date()).getFullYear()		
		let ano = matches[5]
		if (ano > 99 && ano < 1900)
			return null
		if (ano === undefined)	
			ano = anoCorrente
		else {
		  ano = parseInt(ano)			
		  if (ano < 100) {
		  	if (ano <= anoCorrente - 2000)
		  		ano += 2000
		  	else
		  		ano += 1900
		  }	
		}	
		let tipo = matches[1]
		if (tipo === 'DESPTEL') tipo = 'DET'
		if (tipo === 'CIRCTEL') tipo = 'CIT'
		let numero = matches[3]
  	let referencia = `${ tipo } ${ numero }`
		if (tipo !== 'CIT')
			referencia = referencia + '/' + ano
  	return {
  		id: referencia,
  		label: referencia
  	}
  }

  renderItemPosto (item) {
  	return (
      <MenuItem
        key=""
        label={ item.label }
        text={item.label}
        shouldDismissPopover={false}
      />
    )
  }

  minutaEClassificada () {
    const carater = this.state.carater
  	return carater && carater !== 'o'
  }

  handleChange (varName) {
  	return (e) => {
  		let value = e
  		if (e && e.target && e.target.value !== undefined) {
  			value = e.target.value
  		}
  		let stateChange = {}
  		stateChange[varName] = value
  		this.setState(stateChange)
  		this.props.minuta.setProp(varName, value)
  		// Temos que usar setTimeout para o this.setState fazer efeito
  		setTimeout(() => this.validateValue(varName, value), 10)
  	}
  }

  handleClickTipo (tipo) {
  	return (e) => {
  	  this.setState({
  		  tipo
  	  })
  	  this.props.minuta.setProp('tipo', tipo)
  	  if (!this.state.proposito) 
  	  	return
  	  // Só entra aqui se o usuário tiver selecionado um propósito
  	  // Vamos checar se o propósito continua válido a despeito
  	  // da mudança de tipo do documento
  	  //debugger
  	  const propositosDesteTipoDoc = propositos[tipo]
  	  let propositoSelecionado = propositosDesteTipoDoc.find(proposito => proposito.id === this.state.proposito)
  	  if (!propositoSelecionado || !propositoSelecionado[tipo]) {
  	  	this.setState({
  	  		proposito: null
  	  	})
  	  	this.props.minuta.setProp('proposito', '')
  	  }
  	}  
  }

  validateValue (propName) {
 	  const errors = this.props.minuta.validate()
 	  this.setState({
 	  	errors
 	  })  	
  }

	handleButtonClick (e) {
		let errorMsg = this.state.errorMsg ? '' : 'Deu problema'
	  this.setState({
	  	errorMsg
	  })	
	}

  checkTextLength(e, { maxLength, varName, varLabel }) {
  	let value = e.target.value
  	let error = ''
  	if (value && value.length > maxLength) 
  		error = `O ${ varLabel || varName } deve ter até ${ maxLength } caracteres`
  	if (error !== this.state.errors[varName]) {
  		const errors = {
  			...this.state.errors
  		}
  		errors[varName] = error
  		this.setState({
  			errors
  		})
  	}
  }
  
  handleIndiceChange (e) {
  	this.handleTextFieldChange(e, 'indice')
  }

  handleResumoChange (e) {
  	this.handleTextFieldChange(e, 'resumo')
  }

  handleRazoesChange (e) {
  	this.handleTextFieldChange(e, 'razoes')
  }

  handleTextFieldChange (e, propName) {
    const error = this.state.errors[propName]
  	// this.props.minuta[propName] = e.target.value
  	this.props.minuta.setProp(propName, e.target.value)
  	const newError = this.props.minuta.validate(propName)
    if (error !== newError) {
    	const errors = {
    		...this.state.errors
    	}
    	errors[propName] = newError
    	this.setState({
    		errors
    	})
    }
  }

  handleBlurRedator (e) {
  	if (!this.state.redator) return
    const redator = this.state.redator.toUpperCase()
    this.state.minuta.setProp('redator', redator)
  	this.setState({
  		redator,
  		minuta: this.state.minuta 
  	})
  }

  render () {
		return (
			<div className="cabecalho-container custom-scroll">
				<div className="property-bar" className={ (this.props.selectedTab === 'cabecalho' ? 'show' : 'hide') }>

	        <ButtonGroup fill={true}>
	          <Button active={this.state.tipo === 'tel'} text="Telegrama" icon="document" outlined={false} alignText={ Alignment.CENTER } onClick={this.handleClickTipo('tel')} />	        
	          <Button active={this.state.tipo === 'det'} text="Desptel" icon="share" outlined={false} alignText={ Alignment.CENTER } onClick={this.handleClickTipo('det')} />
	          <Button active={this.state.tipo === 'cit'} text="Circtel" icon="feed" outlined={false} alignText={ Alignment.CENTER } onClick={this.handleClickTipo('cit')} />	            
	        </ButtonGroup>

	        <Spacer height={8} />

	   		  <FloatingSelect 
	   		    visible={ this.state.tipo === 'det'}
				    label="Destinatário do desptel"
		    		itemRenderer={this.renderItemPosto}
		    		items={postos}	
		    		multiple={false}
		    		value={ this.state.destinatarioDet }
		    		onChange={ this.handleChange('destinatarioDet') }
		  		></FloatingSelect>  

				  <FloatingSelect 
				    visible={ this.state.tipo === 'cit' }
				    label="Destinatários da circtel"
				    items={[...postos, ...grupos]}	
				    multiple={true}
		    		error={this.state.errors.destinatariosCit}				    				    
				    value={this.state.destinatariosCit}
				    onChange={ this.handleChange('destinatariosCit') }
				  ></FloatingSelect>   		  		

				  <FloatingSelect 
				    label="Distribuição"
				    items={unidades}	
				    multiple={true}
				    highlightFirstTag={true}
				    value={this.state.distribuicao}
				    error={this.state.errors.distribuicao}
				    resetOnSelect={true}
				    onChange={ this.handleChange('distribuicao') }
				  ></FloatingSelect>  		  		

		  		<FloatingSelect 
				    visible={ this.state.tipo === 'det' || this.state.tipo === 'tel' }
				    label="Retransmissão"
				    items={postos}	
				    multiple={true}
		    		error={this.state.errors.rtm}				    
				    value={this.state.rtm}
				    onChange={ this.handleChange('rtm') }
				  ></FloatingSelect>   

		  		<FloatingSelect 
				    visible={ this.state.tipo === 'det' || this.state.tipo === 'tel' }
				    label="Retransmissão para grupo"
				    items={grupos}	
				    multiple={true}
		    		error={this.state.errors.rtmClic}
				    value={this.state.rtmClic}
				    onChange={ this.handleChange('rtmClic') }
				  ></FloatingSelect>   				  

				  <FloatingSelect 
				    label="Indexadores"
				    items={indexadores}	
				    multiple={true}
				    separator="-"
				    value={this.state.indexadores}
				    error={this.state.errors.indexadores}
				    onChange={ this.handleChange('indexadores') }
				  ></FloatingSelect>  				      

			    <FlexDiv>

		        <FloatingNativeSelect
		          label="Caráter"
		          addBlankOption={true}
		          items={grausSigilo}
		          value={this.state.carater}
		          onChange={ this.handleChange('carater') }
		        ></FloatingNativeSelect>  

						<Spacer width={8} />

		        <FloatingNativeSelect
		          label="Prioridade"
		          addBlankOption={true}
		          items={prioridades}
		          value={this.state.prioridade}
		          onChange={ this.handleChange('prioridade') }
		        ></FloatingNativeSelect>   

	        </FlexDiv>

				  <FloatingSelect 
				    label="Referências"
				    items={[]}	
				    multiple={true}
				    value={this.state.referencias}				    
				    createNewItemFromQuery={ this.createNewReferenciaFromQuery }
				    error={this.state.errors.referencias}
				    onChange={ this.handleChange('referencias') }
				  ></FloatingSelect>  	 

				  <FloatingTextarea 
				    label="Índice"
				    rows="3" 
				    onChange={ this.handleIndiceChange }
				    onBlur={ this.handleChange('indice') }
				    error={this.state.errors.indice}
				    value={this.state.indice}></FloatingTextarea>

	        <FlexDiv>

		        <FloatingNativeSelect
		          label="Propósito"
		          addBlankOption={true}
		          style={{ flex: 3 }}
		          items={propositos[this.state.tipo] || []}
		          value={this.state.proposito}
		          onChange={ this.handleChange('proposito') }
		        ></FloatingNativeSelect>  

	          <Spacer width={8} />

		        <FloatingInput
				      label="Iniciais redator"
				      style={{ flex: 2 }}
				      value={this.state.redator}
				      onChange={this.handleChange('redator')}
				      onBlur={ this.handleBlurRedator }
				    />

			    </FlexDiv>				    

	  		</div>  

	  		<div className="property-bar" className={ this.props.selectedTab === 'resumo' ? 'show' : 'hide' }>
	  			<FloatingTextarea 
				    label="Resumo"
				    rows="10" 
				    placeholder="Digite aqui o resumo..."
				    onChange={ this.handleResumoChange } 
				    onBlur={ this.handleChange('resumo') }
				    error={this.state.errors.resumo}
				    value={this.state.resumo}></FloatingTextarea>
				</div>    

				<div className="property-bar" className={ this.props.selectedTab === 'tci' ? 'show' : 'hide' }>

			    <FlexDiv direction="row">

		        <FloatingNativeSelect
		          label="Caráter"
		          addBlankOption={true}
		          items={grausSigilo}
		          value={this.state.carater}
		          onChange={this.handleChange('carater')}
		        ></FloatingNativeSelect>  

						<Spacer width={8} />

				    <FloatingNativeSelect
				      label="Prazo"
				      disabled={!this.minutaEClassificada()}
		          addBlankOption={true}				      				      
				      items={prazos}
				      value={this.state.prazoSigilo}
				      onChange={this.handleChange('prazoSigilo')}
				    ></FloatingNativeSelect>  

	        </FlexDiv>

			    <FloatingNativeSelect
			      label="Base legal"
				    disabled={!this.minutaEClassificada()}
	          addBlankOption={true}			      
			      items={basesLegais}
			      value={this.state.baseLegalSigilo}
			      onChange={this.handleChange('baseLegalSigilo')}
			    ></FloatingNativeSelect>  

	  			<FloatingTextarea 
				    label="Razões para classificação"
				    disabled={!this.minutaEClassificada()}	    
				    rows="5" 
				    onChange={ this.handleRazoesChange } 
				    onBlur={ this.handleChange('razoes') }
				    error={this.state.errors.razoes}
				    value={this.state.razoes}></FloatingTextarea>

			    <FloatingInput
			      label="Autoridade classificadora"
				    disabled={!this.minutaEClassificada()}      
			      value={this.minutaEClassificada() ? this.state.autoridade : ''}
			      onChange={this.handleChange('autoridade')}
			    />

			    <FloatingInput
			      label="Cargo da autoridade classificadora"
				    disabled={!this.minutaEClassificada()}
			      value={this.minutaEClassificada() ? this.state.cargo : ''}
			      onChange={this.handleChange('cargo')}
			    />

				</div>    		

				<div className="property-bar" className={ this.props.selectedTab === 'anexos' ? 'show' : 'hide' }>
				Nada aqui por enquanto
				</div>

			</div>
	  )
  }
}

Cabecalho.propTypes = {
	minuta: PropTypes.instanceOf(Minuta),
	selectedTab: PropTypes.string,
}

export default Cabecalho