import React from "react"
import PropTypes from 'prop-types'

import { Icon, Menu, MenuDivider, MenuItem } from "@blueprintjs/core";
import './SidebarMenu.css'

class SidebarMenu extends React.PureComponent {
  render() {
    const chevronRight = <Icon icon="chevron-right" />
    const menu = (
      <Menu>
        <MenuItem icon="folder-open" text="Carregar modelo" onClick={this.props.onClick('modelos')}/>
        <MenuItem icon="add-to-folder" text="Salvar como modelo..." onClick={this.props.onClick('salvarModelo')} />
        <MenuItem icon="duplicate" text="Salvar uma cópia..." onClick={this.props.onClick('salvarCopia')} />
        <MenuDivider />
        <MenuItem icon="print" text="Imprimir" />
        <MenuDivider />
        <MenuItem icon="pin" text="Salvar esta versão..." onClick={this.props.onClick('salvarVersao')} />
        <MenuItem icon="comparison" text="Ver versões" onClick={this.props.onClick('versoes')} />
        <MenuItem icon="history" text="Histórico" labelElement={chevronRight} onClick={this.props.onClick('historico')} />
        <MenuDivider />                
        <MenuItem icon="small-tick" text="Rubricas" labelElement={chevronRight} onClick={this.props.onClick('rubricas')} />
        <MenuItem icon="document-share" text="Despachar" labelElement={chevronRight} onClick={this.props.onClick('despachos')}/>
        <MenuItem icon="send-message" text="Expedir" labelElement={chevronRight} onClick={this.props.onClick('expedir')} />
      </Menu>
    )
    return menu
  }
}

SidebarMenu.propTypes = {
  onClick: PropTypes.func.isRequired
}

export default SidebarMenu