import React from "react"
import PropTypes from 'prop-types'

import SubmenuPanel from './SubmenuPanel.js'

import { Icon } from '@blueprintjs/core'

import './Historico.css'

function Historico (props) {
  let listaAcoes = []
  for (let i = 0; i < 100; i++) {
  	listaAcoes.push({
  		acao: 'Modificado',
  		icon: 'edit',
  		usuario: 'renato.andrade',
  		data: '20/10/2020 20:36'
  	})
  }
  listaAcoes.push({
  	acao: 'Criado',
  	icon: 'document',
  	usuario: 'fabio.cereda',
  	data: '18/10/2020 10:58'
  })
 
  const listaAcoesElement = listaAcoes.map((acao, index) => {
  	return (
  		<div className="item-historico" key={index}>
  		  <span className="icone-historico"><Icon icon={acao.icon} intent="primary" /></span>
  		  {acao.acao} por <b>{acao.usuario}</b><br />em {acao.data}
  		</div>
  	)	
  })

	return (
		<SubmenuPanel title="Histórico" onClick={props.onBack}>
		  { listaAcoesElement }
		</SubmenuPanel>
  )
}

Historico.propTypes = {
  onBack: PropTypes.func.isRequired
}

export default Historico