import dayjs from 'dayjs'
import { originalData, postos, basesLegais, grausSigilo, prioridades, usuarioAtual } from './globalData.js'

const Consts = {
	LARGURA_TEXTO: 60
}

const Utils = {
  getDateFormatted: (date, format) => {
    return dayjs(date).format(format)
  },

  getCurrentDateFormatted: (format) => {
  	return Utils.getDateFormatted(new Date(), format)
  }
}

function lookupItem (arr, id, campoId='id') {
	const item = arr.find(item => item[campoId] == id)
	return item
}

function lookupItemText (arr, id, campoId='id', campoText='text') {
	const item = lookupItem(arr, id, campoId)
	if (item) 
		return item[campoText] || item
	return ''
}

function splitByWidth (text, width) {
  
  function isSeparator (char) {
    return (char === ' ') || (char === '\n')
  }  
  
  let lines = []
  let start = 0
  do { 
    let thisLine = text.substr(start, width)
    if (start + width >= text.length) {
      lines.push(thisLine.trim())
      break
    }  
    let i
    for (i = thisLine.length - 1; i >= 0 && !isSeparator(thisLine.charAt(i)); i--);
    if (i) {
      thisLine = thisLine.substr(0, i)
      start += i
    }  
    else {
      start += width
    }  
    lines.push(thisLine.trim())
  } while (start < text.length)
  return lines
}  

function obterNomePosto (siglaPosto) {
	return lookupItemText(postos, siglaPosto)
}

function obterCarater (idCarater) {
	return lookupItemText(grausSigilo, idCarater).toUpperCase()
}

function obterPrioridade (idPrioridade) {
  return lookupItemText(prioridades, idPrioridade).toUpperCase()
}

function tratarIndice (indice) {
	return indice
}

function textRetransmissao (rtm) {
	if (!rtm.length) return ''
	const nomesPostos = rtm.map(siglaPosto => obterNomePosto(siglaPosto))
  let texto = ''
  for (let i = 0; i < nomesPostos.length; i++) {
    if (i) {
    	const separador = i < nomesPostos.length - 1 ? ', ' : ' e '
    	texto = texto + separador
    }
    texto = texto + nomesPostos[i]
  }
  return texto
}

function linesResumo (resumo) {
	return resumo || ''
}

function prepararArquivoExpedicao (minuta) {
	const campos = [{
		id: null,
		func: () => {
			let abertura = ''
			switch (minuta.tipo) {
				case 'tel': 
				  abertura = `De ${obterNomePosto(usuarioAtual.posto)} para Exteriores`
				  break
        case 'det':
          abertura = `Da SERE para ${obterNomePosto(usuarioAtual.posto)}`
          break
        case 'cit':
          abertura = `Da SERE para Circular Telegráfica`
      }    
      abertura = abertura + ` em ${Utils.getCurrentDateFormatted('DD/MM/YYYY')}\r\n`
      return abertura
    }  
	}, {
		id: 'CODI',
		func: () => ''
	}, {
		id: 'CARAT',
		func: () => obterCarater(minuta.carater)
	}, {
		id: 'DEXP',
		func: () => {
			if (!minuta.prazoSigilo)
				return null
			let now = new Date()
			let prazo = new Date(now.getFullYear() + Number(minuta.prazoSigilo), now.getMonth(), now.getDate())
			return Utils.getDateFormatted(prazo, 'DD/MM/YYYY')
		}
	}, {
		id: 'BLEGIS',
		func: () => {
			if (!minuta.baseLegalSigilo)
				return ''
			let baselegal = lookupItemText(basesLegais, minuta.baseLegalSigilo)
			console.log(baselegal)
			baselegal = baselegal.replace('Lei 12.527/2011 ', '')
			return baselegal
		}
	}, {
		id: 'PRIOR',
		func: () => obterPrioridade(minuta.prioridade)
	}, {
		id: 'DISTR',
		func: () => minuta.distribuicao.join('/')
	}, {
		id: 'DESCR',
		// func: () => minuta.classif.replace(/\s+/, "")
		func: () => minuta.indexadores.join('-')
	}, {
		id: 'RTM',
		func: () => {
			if (!minuta.rtm)
				return ''
			return minuta.rtm.join(',').toUpperCase()
		}
	}, {
		id: 'RTM/CLIC',
		func: () => {
			return minuta.rtmClic
		}
	}, {
		id: 'REF/ADIT',
		func: () => {
			/**
			 * New Regex MAN-3787
			 * 
			 * Valida as iniciais ^(tel|desptel|det|circtel|cit)
			 * Valida os numeros do documento \s(\d{1,6})
			 * Valida o ano de referencia Opcional ([\s|\/][\d]{4}?)*$
			 * 
			 * ^(tel|desptel|det|circtel|cit)\s(\d{1,6})([\s|\/][\d]{4}?)*$
			 */
			let ref = minuta.referencias,
				arrItem = [],
				date = new Date(),
				thisYear = date.getFullYear(),
				MATCHES = /(TEL|DESPTEL|DET|CIRCTEL|CIT)\s(\d{1,6})([\s|/][\d]{4}?)*$/i
			if (!ref || !ref.length)
				return null
			ref = ref.map(val => {
				arrItem = val.match(MATCHES)
				typeof arrItem[3] === 'undefined' ? arrItem[3] = ((arrItem[1] !== 'CIT') ? thisYear : '') : arrItem[3] = parseInt(arrItem[3].replace('/', ''))
				return arrItem.splice(1).join(' ').trim()
			})
			return ref.join(',')
		}
	}, {

// NOVO CAMPO: Propósito

    id: 'PROPSTO',
    func: () => minuta.proposito || ''
	}, {	
		id: 'CATEG',
		func: () => 'MG' 
	}, {
// NOVO CAMPO: Formato
    id: 'FMT',
    func: () => 'REML'
  }, {				
		id: null,
		func: () => `\r\n//\r\n${tratarIndice(minuta.indice)}\r\n//\r\n`
	}, {
		id: null,
		func: () => obterNomePosto(minuta.rtmClic)
	}, {
		id: null,
		func: () => minuta.rtm.length > 0 ? `Retransmissão automática para ${textRetransmissao(minuta.rtm)} \r\n` : ''
	}, {
		id: 'RESUMO',
		func: () => {
			return `\r\n${linesResumo(minuta.resumo)}`
		}
	}, {
		id: null,
		func: () => {
			let strTexto = minuta.texto.replace(/(“|”)/g, '"')
			let texto = splitByWidth(strTexto, Consts.LARGURA_TEXTO).join('\r\n')
			let iniciais = (minuta.redator || '').toUpperCase()
			return `\r\n\r\n${texto}\r\n\r\n${iniciais}`
		}
	}]

	let linhas = campos.map(campo => {
		let linha = campo.id ? `${campo.id}=` : ''
		let conteudo = campo.func()
		if (conteudo) {
			linha = linha + conteudo
		}
		return linha
	})

	return linhas.join('\r\n')
}

function prepararObjetoExpedicao (minuta) {
	let destinatarios = minuta.tipo === 'det' ? minuta.rtm : minuta.destinatariosCit
	destinatarios = destinatarios.map(dest => lookupItem(originalData.postos, dest, 'codigoUnidade'))

  let objExpedicao = {
    classificacao: minuta.indexadores.map(indexador => lookupItem(originalData.indexadores, indexador, 'sigla')),
    coBaseLegal: minuta.baseLegalSigilo,
    coPrioridade: minuta.prioridade,
    destinatario: minuta.tipo === 'det' ? [ lookupItem(originalData.postos, minuta.destinatarioDet, 'codigoUnidade') ] : [],
    distribuicao: minuta.distribuicao.map(siglaUnidade => lookupItem(originalData.unidades, siglaUnidade, 'codigoUnidade')),
    dsBaseLegal: lookupItemText(basesLegais, minuta.baseLegalSigilo, 'id'),
    dtOutras: '',
    idCarater: minuta.carater,
    noBaseLegal: minuta.baseLegalSigilo,
    prazoSigilo: minuta.prazoSigilo,
    sgCategoria: 'MG',

    tpDocumento: minuta.tipo.toUpperCase(),
    dsResumo: (minuta.resumo || '').replace(/\n/g, '\r\n'),
    dsIndice: (minuta.indice || '').replace(/\n/g, '\r\n'),
    dsSiglaNome: minuta.redator,
    sgFormatoTexto: 'REML',
    dsTexto: minuta.texto.replace(/\n/g, '\r\n'),
    noCarater: lookupItemText(originalData.grausSigilo, minuta.carater, 'id'),
    noPrioridade: lookupItemText(prioridades, minuta.prioridade),
    dsRetransmissao: minuta.tipo === 'det' ? destinatarios : '',
    referencia: minuta.referencias.join(', '),
    idCategoria: 1,
    noLogin: minuta.dsNome,
    dsDestinatario: destinatarios,
    coPrazoBaseLegal: minuta.prazoSigilo,
    coProposito: minuta.proposito,
    noProposito: lookupItemText(originalData.propositos, minuta.proposito, 'idProposito', 'descricao'),
  }
  return objExpedicao
}

