function removerAcentos (str = '') {
  return str.toString().normalize("NFD").replace(/[\u0300-\u036f]/g, "")
}

function randomInteger (min=0, max=1) {
  let number = Math.random() * (max - min) + min 
	number = Math.floor(number) 
	return number
}

function gerarUsuarios (numUsuarios = 100) {
	let usuarios = []
	for (let i = 0; i < numUsuarios; i++) {
	  const primeiroNome = randomNomes[randomInteger(0, randomNomes.length)]
	  let numSobrenomes = randomInteger(1, 4)
	  let sobrenomes = []
	  for (let j = 0; j < numSobrenomes; j++) {
	  	sobrenomes.push(randomSobrenomes[randomInteger(0, randomSobrenomes.length)])
	  }
    usuarios.push({
    	nome: primeiroNome + ' ' + sobrenomes.join(' '),  //primeiroNome + ' ' + segundoNome + ' ' + ultimoNome,
    	login: removerAcentos((primeiroNome + '.' + sobrenomes.pop())).toLowerCase() // removerAcentos((primeiroNome + '.' + ultimoNome)).toLowerCase(),
    })
	}
	return usuarios
}

function atribuirUnidadesAUsuarios () {
  // Esta função atribui unidades aos usuários mocados
  // As unidades não são atribuídas quando a lista de usuários
  // mocados é criada porque os arrays unidades e postos são
  // carregados depois que a lista de usuários é criada.
  // Claro que tudo isso é feito apenas para simular uma chamada
  // ao servidor para obter os usuários em buscarUsuarios(),
  // e portanto é irrelevante para o produto final
	let unidadesEPostos = [...unidades, ...postos].map(unidade => unidade.label || unidade.text)
	usuarios.forEach(usuario => usuario.unidade = unidadesEPostos[randomInteger(0, unidadesEPostos.length)])
}

