function carregarDataset (dataset, callback) {
	axios.get(`/data/${ dataset }.json`).then(response => {
		let data = response.data
		callback(data)
	})
}

function dataToUnidade (unidade) {
	return {
	  label: unidade.codigoUnidade,
		text: unidade.descricao.match(/\((.+)\)/)[1]
  }
}

function dataToPosto (posto) {
	return {
		id: posto.codigoUnidade,
		text: posto.noPostoAbreviado
	}
}

function dataToProposito (proposito) {
	return {
		id: proposito.idProposito,
		text: proposito.descricao,
	}
}

function limparArray (arr) {
  for (let i = arr.length - 1; i >= 0; i--) {
  	arr.pop()
  }	
}

function importarDadosParaArray (data, arr, mapFunc = x => x, filterFunc = x => true) {
	limparArray(arr)
	data.filter(filterFunc).map(mapFunc).forEach(item => arr.push(item))
}

carregarDataset('unidades', (data) => {
	originalData.unidades = data.grupo_unidade
  importarDadosParaArray(data.grupo_unidade, unidades, dataToUnidade)
})

carregarDataset('postos', data => {
  originalData.postos = data.grupo_unidade	
	importarDadosParaArray(data.grupo_unidade, postos, dataToPosto)
})

carregarDataset('indexadores', data => {
	let items = data.classificacao.map(item => {
		return {
			...item,
			label: item.sigla,
			text: item.nameClassificacao.match(/-\s(.+)$/)[1]
		}
	})
	items = items.sort((a, b) => {
		if (a.tipoClassificacao < b.tipoClassificacao) return -1
		if (a.tipoClassificacao > b.tipoClassificacao) return 1
		return a.text < b.text ? -1 : 1
	})	 
	originalData.indexadores = items
	importarDadosParaArray(items, indexadores)
})

carregarDataset('baselegal', data => {
  originalData.basesLegais = data.BaseLegal
	limparArray(basesLegais)
	data.BaseLegal.map(baselegal => {
    return {
    	id: baselegal.cmpCoBaseLegal,
    	text: baselegal.cmpDcBaseLegal
    }
	}).forEach(baselegal => basesLegais.push(baselegal))
})

carregarDataset('propositos', data => {
  originalData.propositos = data.propositos
	importarDadosParaArray(data.propositos.filter(proposito => proposito.tel), propositos.tel, dataToProposito)	
	importarDadosParaArray(data.propositos.filter(proposito => proposito.det), propositos.det, dataToProposito)	
	importarDadosParaArray(data.propositos.filter(proposito => proposito.cit), propositos.cit, dataToProposito)	
})


