import React from 'react'
import PropTypes from 'prop-types'

import { Intent } from '@blueprintjs/core'

import Slider from './ui/Slider.js'
import SliderContainer from './ui/SliderContainer.js'	

import SidebarMenu from './sidebar/SidebarMenu.js'
import Despachos from './sidebar/Despachos.js'
import Expedir from './sidebar/Expedir.js'
import Historico from './sidebar/Historico.js'
import Rubricas from './sidebar/Rubricas.js'
import DialogInputNome from './sidebar/DialogInputNome.js'
import DialogMinutas from './sidebar/DialogMinutas.js'
import DialogSalvarModelo from './sidebar/DialogSalvarModelo.js'
import Minuta from './Minuta.js'
import { carregarModelos, carregarVersoes } from './lib/minutaData.js'

class Sidebar extends React.Component {
	constructor (props) {
		super()
		this.state = {
			openPane: 'menu',
			openDialog: '',
		}
		this.handleMenuClick = this.handleMenuClick.bind(this)	
		this.handleSalvarCopia = this.handleSalvarCopia.bind(this)
		this.handleSalvarModelo = this.handleSalvarModelo.bind(this)
		this.handleSalvarVersao = this.handleSalvarVersao.bind(this)
		this.backToMenu = this.backToMenu.bind(this)	
		this.openDialog = this.openDialog.bind(this)
		this.closeDialog = this.closeDialog.bind(this)
		this.openPane = this.openPane.bind(this)
  } 

  handleMenuClick (paneToOpen) {
  	if (['modelos', 'versoes', 'salvarCopia', 'salvarModelo', 'salvarVersao'].includes(paneToOpen))
  		return (e) => this.openDialog(paneToOpen)
  	else
  	  return (e) => this.openPane(paneToOpen)
  }

  openDialog (dialogToOpen) {
  	this.setState({
  		openDialog: dialogToOpen
  	})
  	this.forceUpdate()
  }

  closeDialog () {
  	this.setState({
  		openDialog: ''
  	})
  }

  openPane (paneToOpen) {
    this.setState({
    	openPane: paneToOpen
    })   
    this.forceUpdate()	  	
  }

  backToMenu () {
  	this.openPane('menu')
  }

  loadModelos () {
    return carregarModelos()
  }

  loadMinuta (minutaObj) {
  	return new Promise (function (resolve, reject) {
  		const modelo = {
  			tipo: 'cit',
	   		distribuicao: ['DEUC', 'DMAC'],
	   		indexadores: ['PEXT', 'BRAS', 'EUA'],
	   		destinatarioDet: 'USABREM',
    		rtm: ['USABREM', 'CANBREM'],
    		proposito: 'Informo e rogo providências',
    		prioridade: 'u',
    		carater: 's',
        redator: 'FACC',
    		referencias: ['TEL 1234', 'CIT 134233', 'det 12/2020'],
    		indice: 'Modelo. Índice',
    		texto: `<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nunc sit amet egestas tortor. Nam a odio facilisis, pellentesque orci sed, vestibulum nibh. Sed sapien mauris, mattis quis mollis eget, molestie et sem. Nullam condimentum congue commodo. Maecenas ac quam at magna tempor congue id ac quam. Ut velit nunc, sagittis sit amet odio vitae, maximus lacinia orci. Sed bibendum neque a nisl sagittis tempus. Aenean finibus finibus sagittis. Mauris posuere elit et auctor tempor. Aenean scelerisque fringilla ante at maximus.</p>
					<p>2. Quisque quis laoreet purus. Mauris cursus neque non elit posuere, id pretium felis tempor. Cras varius eleifend ipsum id malesuada. Proin dictum, purus quis aliquet placerat, felis lorem lacinia turpis, ut convallis augue nisi eget odio. Sed dui nisi, vehicula eget posuere sed, dignissim non lacus. Nulla ac nibh purus. Aenean sit amet ornare orci, eu rutrum sem. Ut elementum imperdiet nisl, eu ullamcorper sem laoreet in. Donec ut ornare lacus. Ut sed lectus lectus. Suspendisse vestibulum, justo non condimentum fringilla, turpis eros commodo ex, in iaculis velit dui dignissim augue. Suspendisse non magna urna. Vivamus consectetur metus in justo mollis mattis.</p>
					<p>3. Quisque quis laoreet purus. Mauris cursus neque non elit posuere, id pretium felis tempor. Cras varius eleifend ipsum id malesuada. Proin dictum, purus quis aliquet placerat, felis lorem lacinia turpis, ut convallis augue nisi eget odio. Sed dui nisi, vehicula eget posuere sed, dignissim non lacus. Nulla ac nibh purus. Aenean sit amet ornare orci, eu rutrum sem. Ut elementum imperdiet nisl, eu ullamcorper sem laoreet in. Donec ut ornare lacus. Ut sed lectus lectus. Suspendisse vestibulum, justo non condimentum fringilla, turpis eros commodo ex, in iaculis velit dui dignissim augue. Suspendisse non magna urna. Vivamus consectetur metus in justo mollis mattis.</p>
					<p>EXTERIORES</p>`	
	    }
	    setTimeout(() => resolve(modelo), 2000)
  	})
  }

  loadVersoes () {
    return carregarVersoes()
  }

  handleSalvarCopia (e) {
    this.props.onSalvarCopia(e)
  }

  handleSalvarModelo (e) {
  	this.props.onSalvarModelo(e)
  }

  handleSalvarVersao (e) {
  	this.props.onSalvarVersao(e)
  }  
   
  render () {
  	const showRubricas  = this.state.openPane === 'rubricas'
    const showDespachos = this.state.openPane === 'despachos'
    const showExpedir   = this.state.openPane === 'expedir'
    const showHistorico = this.state.openPane === 'historico'
  	const modelosActions = [{
  		text: 'Usar este modelo',
  		intent: Intent.PRIMARY,
  		handler: (e) => { 
  			this.props.onUsarModelo(e)
  			return true 
  		}
  	}]
  	const versoesActions = [{
  		text: 'Usar esta versão',
  		intent: Intent.PRIMARY,
  		handler: (e) => { 
  			this.props.onUsarVersao(e)
  			return true 
  		}
  	}]  	
  	return (
	  	<SliderContainer>
		  	<Slider show={ this.state.openPane === 'menu' } position="left">
		  		<SidebarMenu onClick={this.handleMenuClick}/>
		  	</Slider>
		  	<Slider show={ showRubricas } position="right">
		  		<Rubricas onBack={this.backToMenu} show={ showRubricas } minuta={ this.props.minuta } />
				</Slider>
		  	<Slider show={ showHistorico } position="right">
		  		<Historico onBack={this.backToMenu} show={ showHistorico } />
				</Slider>			
		  	<Slider show={ showDespachos } position="right">
		  		<Despachos onBack={this.backToMenu} show={ showDespachos } />
				</Slider>		
        <Slider show={ this.state.openPane === 'expedir' } position="right">
          <Expedir onBack={this.backToMenu} show={ showExpedir } minuta={ this.props.minuta } />
        </Slider>  
				<DialogMinutas 
				  show={ this.state.openDialog === 'modelos' } 
				  onClose={ this.closeDialog } 
				  grupos={ this.loadModelos  }
				  minuta={ this.loadMinuta }
				  actions={ modelosActions }/>
				<DialogMinutas
				  show={ this.state.openDialog === 'versoes' }
				  title="Versões desta minuta"
				  placeholder="Selecione uma versão na lista à esquerda"
				  onClose={ this.closeDialog }
				  minutas={ this.loadVersoes }
				  minuta={ this.loadMinuta }
				  actions={ versoesActions }/>
				<DialogInputNome
				  show={ this.state.openDialog === 'salvarVersao' }  
				  title="Salvar esta versão"
				  onClose={ this.closeDialog }
				  onChange={ this.handleSalvarVersao }
				/>  
				<DialogInputNome
				  show={ this.state.openDialog === 'salvarCopia' }  
				  title="Salvar uma cópia desta minuta"
				  label="Indique o título da cópia que será criada:"
				  onClose={ this.closeDialog }
				  onChange={ this.handleSalvarCopia }
				/> 
				<DialogSalvarModelo
				  show={ this.state.openDialog === 'salvarModelo' }
				  onClose={ this.closeDialog }
				  onChange={ this.handleSalvarModelo }				
				/>  
			</SliderContainer>
		)	
  }
}

Sidebar.propTypes = {
  minuta: PropTypes.instanceOf(Minuta).isRequired,
  onSalvarCopia: PropTypes.func,
  onSalvarModelo: PropTypes.func,
  onSalvarVersao: PropTypes.func,
  onUsarModelo: PropTypes.func,
  onUsarVersao: PropTypes.func,
}

export default Sidebar
  
  